import React from "react";

const Members = () => {
  const tableRow = () => {
    return (
      <tr style={{ fontWeight: "300", fontSize: "13px" }}>
        <td>Tagger Name</td>
        <td>Tagger</td>
        <td className="justify-content-center">
          <input type="submit" value="delete" className="danger" name="" />
        </td>
      </tr>
    );
  };

  return (
    <div className="mt-5 py-5">
      <div className="row">
        <div className="col-md-8 offset-md-2">
          <p className="heads">Project Name</p>
        </div>
      </div>
      <div className="row">
        <div className="col-md-8 offset-md-2">
          <div className="card shadow-sm">
            <div className="card-body p-33">
              <div className="row">
                <table className="table">
                  <thead>
                    <tr>
                      <th className="proj-heads-size">Tagger Name</th>
                      <th className="proj-detail-size">Role</th>
                      <th></th>
                    </tr>
                  </thead>
                  <tbody>
                    {tableRow()}
                    {tableRow()}
                    {tableRow()}
                    {tableRow()}
                    {tableRow()}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Members;
