import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

export default function ProjectDetails() {
  const dispatch = useDispatch();
  const state = useSelector((state) => state.util);
  const [values, setvalues] = useState({
    project_name: state.newProject.project_name,
    company: state.newProject.company,
    additional_details: state.newProject.additional_details,
    industry: state.newProject.industry,
    purpose: state.newProject.purpose,
  });
  const handleInput = (name) => (e) => {
    setvalues({ ...values, [name]: e.target.value });
    if (name === "project_name") {
      dispatch({ type: "ADD_PROJECT_NAME", value: e.target.value });
    }
  };

  const handleNext = () => {
    dispatch({ type: "ACTIVE_TAB", tab: "dataset" });
    dispatch({ type: "SET_PROJECT", values: values });
  };

  useEffect(() => {
    dispatch({
      type: "DATASET_NAME_ADDED",
      bool: false,
    });
  }, []);

  return (
    <div className="mt-5">
      <div className="mt-5">
        <form>
          <div className="row">
            <div className="col-md-8 ml-5 mt-5">
              <div className="row mb-2">
                <div className="col-md-6">
                  <div className="form-group">
                    <label
                      className="submenu color-blue"
                      style={{ fontWeight: "300", fontSize: "14px" }}
                    >
                      Project Name
                      <span
                        className="text-danger mt-3"
                        style={{ fontSize: "16px", fontWeight: "bold" }}
                      >
                        *
                      </span>
                    </label>
                    <input
                      type="text"
                      className="form-control form-control-line blue-line"
                      name="user"
                      required={true}
                      onChange={handleInput("project_name")}
                      value={values.project_name}
                    />
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="form-group">
                    <label
                      style={{ fontWeight: "300", fontSize: "14px" }}
                      className="submenu color-blue"
                    >
                      Industry
                    </label>
                    <input
                      type="text"
                      className="form-control form-control-line blue-line"
                      name="user"
                      onChange={handleInput("industry")}
                      value={values.industry}
                    />
                  </div>
                </div>
              </div>
              <div className="row mb-2">
                <div className="col-md-6">
                  <div className="form-group">
                    <label
                      style={{ fontWeight: "300", fontSize: "14px" }}
                      className="submenu color-blue"
                    >
                      Company
                    </label>
                    <input
                      type="text"
                      className="form-control form-control-line blue-line"
                      name="user"
                      onChange={handleInput("company")}
                      value={values.company}
                    />
                  </div>
                </div>
                <div className="col-md-6">
                  <div className="form-group">
                    <label
                      style={{ fontWeight: "300", fontSize: "14px" }}
                      className="submenu color-blue"
                    >
                      Purpose
                    </label>
                    <input
                      type="text"
                      className="form-control form-control-line blue-line"
                      name="user"
                      onChange={handleInput("purpose")}
                      value={values.purpose}
                    />
                  </div>
                </div>
              </div>
              <div className="row mb-2">
                <div className="col-md-6">
                  <div className="form-group">
                    <label
                      style={{ fontWeight: "300", fontSize: "14px" }}
                      className="submenu color-blue"
                    >
                      Additional Details
                    </label>
                    <input
                      type="text"
                      className="form-control form-control-line blue-line"
                      name="user"
                      onChange={handleInput("additional_details")}
                      value={values.additional_details}
                    />
                  </div>
                </div>
              </div>
              <div className="row mt-3">
                <div className="col-md-6">
                  <button
                    type="submit"
                    style={{
                      borderRadius: "4px",
                      backgroundColor: "#4216D2",
                      boxShadow: "0px 0px 0px 0px",
                      fontSize: "small",
                    }}
                    onClick={handleNext}
                    className="btn btn-block  text-center"
                    disabled={values.project_name === ""}
                  >
                    Next
                  </button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}
