import React, { PureComponent } from "react";
import "../../static/css/custom.css";

export class RecentProjects extends PureComponent {
  render() {
    return (
      <div className="row">
        <p
          className="heads ml-4 pt-2"
          style={{ fontWeight: "400", fontSize: "16px" }}
        >
          Recent Projects
          <span className="">
            <i
              className="fa fa-chevron-right recent-icon"
              aria-hidden="true"
            ></i>
          </span>
        </p>
      </div>
    );
  }
}

export default RecentProjects;
