import React, { useState } from "react";
import '../../static/css/custom.css';
import { useHistory, Link } from 'react-router-dom';
import { publicApi } from '../../Util/API';
import { useDispatch } from 'react-redux';
import { recievedUser } from '../../redux/user/actions'
import Alert from '@material-ui/lab/Alert'



const LoginForm = () => {
  const [toggle, settoggle] = useState(false);
  const [values, setvalues] = useState({
    username: '',
    password: '',
  });
  const [user, setuser] = useState({})
  const [error, setError] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();


  const handleLogin = async (e) => {
    e.preventDefault();
    let res = await publicApi('/api/users/login/', { 'username': values.username, 'password': values.password }, 'post');
    setuser(res);
    console.log(res);
    if (res.status == 200) {
      dispatch(recievedUser(res.body));
      history.push('/dashboard');return;
    }
    setError(true);

  }

  const handleInput = name => e => {
    setvalues({ ...values, [name]: e.target.value });
  }


  const forgetPassword = () => {
    return (
      <div className="card-login-page mt-5">

        <form method="post" action="">
          <div className="form-group">
            <label className="label-login-page">
              Enter Your E-mail / Username
            </label>
            <input
              type="email"

              className="form-control form-control-line"
              name="user"

            />
          </div>
          <div className="row">
            <div className="col-md-6">
              <h5 className="signup-text" onClick={() => settoggle(false)}>
                Login
              </h5>
            </div>
            <div className="col-md-6 text-right">
              <h5 className="signup-text">Sign Up</h5>
            </div>
          </div>
          <input

            type="submit"
            className="btn-login-page btn-sm btn-block"
            value="Send Verfication Code"
            name="submit"
          />
        </form>
      </div>
    );
  };

  return toggle ? (
    forgetPassword()
  ) : (
    <div className="card-login-page">
      <form onSubmit={handleLogin}>
        {error ? <Alert severity="error"><p className="">{user.body.Message}</p></Alert> : null}
        <div className="form-group">
          <label className="label-login-page">E-mail / Username</label>
          <input
            required={true}
            onChange={handleInput('username')}
            type="text"
            className="form-control form-control-line"
            name="user"
            style={error ? { borderBottom: '2px solid red' } : {}}
          />
        </div>
        <div className="form-group">
          <label className="label-login-page">Password</label>
          <input
            required={true}
            onChange={handleInput('password')}
            type="password"
            className="form-control form-control-line"
            name="password"
          />
        </div>
        <div className="row">
          <div className="col-md-6">
            <Link to="/signup"><h5 className="signup-text">Sign Up</h5></Link>
          </div>
          <div className="col-md-6">
            <h5
              className="forgot-psw-login-page"
              onClick={() => settoggle(true)}
            >
              Forgot Password
            </h5>
          </div>
        </div>
        <input
          type="submit"
          className="btn-login-page btn-sm btn-block"
          value="LOG IN"
          name="submit"
        />
      </form>
    </div>
  );
};

export default LoginForm;
