import React from 'react';
import InProjects from "./InProjectsInfo";
import MostRecent from "../../UI/shared/MostRecent";
import SearchBox from "../../UI/shared/SearchBox";
import DatasetImageCard from "../../UI/Cards/DatasetImageCard";
import Title from "./Title";
import DatasetDetails from "./DatasetDetails";
import { useSelector, useDispatch } from "react-redux";




export default function Content() {
const state = useSelector((state) => state.dataset);

    return (
        <div className="container p-5 mt-5">
            <div className="col-md-12">
                <div className="row">
                    <div className="col-md-8">
                        <div className="row">
                            <SearchBox />
                            <Title title={state.dataset.dataset_name} count={state.images.length}/>
                            <MostRecent />
                        </div>
                        <div
                            className="row  background-white mt-2"
                            style={{ borderRadius: "10px" }}
                        >
                            {state.images.map((image, index) => {
                                return <DatasetImageCard image={image} />;
                            })}
                        </div>
                    </div>
                    <div className="col-md-4">
                        <InProjects />
                        <DatasetDetails />
                    </div>
                </div>
            </div>
        </div>
    )
}
