import React from "react";
import { useSelector } from "react-redux";
import ProjectCard from "../../UI/Cards/ProjectCard";
import ButtonLarge from "../../UI/Buttons/Button_Lg";
import ProgressBar from "./ProgressCard";
import ProjectDetails from "./ProjectDetails";
import RecentProjects from "./RecentProject";

const Content = () => {
  const state = useSelector((s) => s.user);

  return (
    <div className="container mt-5 py-5  mb-5">
      <RecentProjects />
      <div className="row px-1 mt-2">
        {state.projects.map((project, index) => {
          if (index <= 3) {
            return <ProjectCard project={project} className="col-md-2 col-lg-2 col-xl-2"/>;
          }
        })}
        
        <ButtonLarge
          color="#4216d2"
          text="New Project"
          icon="fa fa-plus-circle"
        />
      </div>
      <div className="row mb-5" style={{ marginTop: "10px" }}>
        <ProgressBar />
        <ProjectDetails />
      </div>
    </div>
  );
};

export default Content;
