import React from "react";

const Performance = () => {
  const tableRow = () => {
    return (
      <tr  style={{ fontWeight: "300", fontSize: "13px" }}>
        <td>Tagger Name</td>
        <td>25</td>
        <td>00:00:25</td>
        <td>00:45:48</td>
        <td>
          <div
            className="progress mt-3"
            style={{ height: "7px", cursor: "pointer" }}
          >
            <div
              class="progress-bar"
              role="progressbar"
              style={{ width: "67%", backgroundColor: "#161d53" }}
              aria-valuenow="25"
              aria-valuemin="0"
              aria-valuemax="100"
            ></div>
          </div>
        </td>
        <td className="text-right ml-5">
          <input type="submit" value="View" className="viewbt ml-5" name="" />
        </td>
      </tr>
    );
  };

  return (
    <div className="mt-5 py-5 ">
      <div className="row">
        <div className="col-md-8 offset-md-2">
          <p className="heads mt-4 ">
            Project Name
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-md-8 offset-md-2">
          <div className="card shadow-sm">
            <div className="card-body p-33">
              <div className="row">
                <div className="col-md-12">
                  <table className="table">
                    <thead>
                      <tr>
                        <th className="proj-detail-size">Tagger Name</th>
                        <th className="proj-detail-size">Labels</th>
                        <th className="proj-detail-size">Time per Label</th>
                        <th className="proj-detail-size">Total Time</th>
                        <th className="proj-detail-size">Benchmark Score</th>
                        <th className="proj-detail-size"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {tableRow()}
                      {tableRow()}
                      {tableRow()}
                      {tableRow()}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      </div>
    
  );
};

export default Performance;
