import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { privateApi } from ".././Util/API";
import { recievedImages } from "../redux/project/actions";
import { isAuthenticated } from '../Util/Auth';
import Layout from "../UI/Layouts/Layout";
import Content from '../components/Project-Images/Content';

const LocuslearnProjectImages = () => {
  const dispatch = useDispatch();
  const state = useSelector((state) => state.project);
  const handleRequest = async () => {
    let res = await privateApi(`/api/images/New_ImageAPI/?userId=${isAuthenticated().user.user_id}&projectId=${state.project.project_id}&userType=${state.project.user_type}`, {}, "get");
    if (res.status === 200) {
      dispatch(recievedImages(res.body));
      return;
    }
  };

  useEffect(() => {
    handleRequest();
  }, []);

  return (
    <Layout content={Content} filter={true}/>
  );
};

export default LocuslearnProjectImages;
