import React from "react";
import { Avatar, ArrowRightIcon } from "evergreen-ui";
import { useDispatch } from "react-redux";
import { isAuthenticated } from "../../Util/Auth";
import { Link } from "react-router-dom";

const ShrinkSidebar = () => {
  const dispatch = useDispatch();
  const style1 = () => {
    return {
      position: "fixed",
      top: "10%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "5%",
      height: "100%",
      backgroundColor: "#000840",
    };
  };

  const style2 = () => {
    return {
      position: "fixed",
      top: "95%",
      left: "0",
      right: "0",
      bottom: "10%",
      width: "5%",
      height: "5%",
      backgroundColor: "#1f4287",
      color: "white",
    };
  };
  return (
    <div style={style1()}>
      <div
        className="text-center "
        onClick={() => dispatch({ type: "TOGGLE_SIDEBAR", bool: false })}
        style={style2()}
      >
        <ArrowRightIcon size={20} marginTop={15} />
      </div>
      <div className="row py-5 mt-5 ">
        <div className="col-md-7 offset-md-2  mt-5">
          <Avatar
            name={isAuthenticated().user.username}
            size={28}
            marginLeft={7}
          />
        </div>
        <div className="col-md-6 offset-md-3 mt-5">
          <Link to="/dashboard">
            <i
              className="fa fa-tachometer"
              style={{
                color:
                  window.location.pathname === "/dashboard"
                    ? "rgb(251, 41, 253)"
                    : "white",
                fontSize: "20px",
              }}
              aria-hidden="true"
            ></i>
          </Link>
        </div>
        
          <div className="col-md-6 offset-md-3 mt-5">
          <Link to="/admin-collaborators">
            <i
              className="fa fa-users "
              style={{
                color:
                  window.location.pathname === "/admin-collaborators"
                    ? "rgb(251, 41, 253)"
                    : "white",
                fontSize: "20px",
              }}
              aria-hidden="true"
            ></i>
        </Link>
          </div>
        <div className="col-md-6 offset-md-3 mt-5">
          <i
            className="fa fa-bell"
            style={{ color: "white", fontSize: "20px" }}
            aria-hidden="true"
          ></i>
        </div>
      </div>
    </div>
  );
};

export default ShrinkSidebar;
