import {
    RESET_MATRIX,
    TRANSLATE_CANVAS,
    ROTATE_CANVAS,
    SCALE_CANVAS
} from './constants'

const initialState = {
    zoomIntensity: 0.1,
    scale: 1,
    zoomArray: [],
    translateX: 0,
    translateY: 0,
    transformationMatrix: [1, 0, 0, 1, 0, 0],
}



const transformationMatrixReducer = (state = initialState, action) => {
    switch (action.type) {
        case RESET_MATRIX:
            return {
                ...state,
                transformationMatrix: [1, 0, 0, 1, 0, 0]

            }
        case TRANSLATE_CANVAS:
            var mat = [1, 0, 0, 1, action.x, action.y];
            multiply(state.transformationMatrix, mat);
            return {
                ...state,
                transformationMatrix: [1, 0, 0, 1, 0, 0]

            }
        case ROTATE_CANVAS:
            var c = Math.cos(action.rAngle);
            var s = Math.sin(action.rAngle);
            var mat = [c, s, -s, c, 0, 0];
            multiply(state.transformationMatrix, mat);
            return {
                ...state,
                transformationMatrix: [1, 0, 0, 1, 0, 0]

            }
        case SCALE_CANVAS:
            var mat = [action.x, 0, 0, action.y, 0, 0];
            multiply(state.transformationMatrix, mat);
            return {
                ...state,
                transformationMatrix: [1, 0, 0, 1, 0, 0]

            }
        default: return state
    }
}

function multiply(m, mat) {
    var m0 = m[0] * mat[0] + m[2] * mat[1];
    var m1 = m[1] * mat[0] + m[3] * mat[1];
    var m2 = m[0] * mat[2] + m[2] * mat[3];
    var m3 = m[1] * mat[2] + m[3] * mat[3];
    var m4 = m[0] * mat[4] + m[2] * mat[5] + m[4];
    var m5 = m[1] * mat[4] + m[3] * mat[5] + m[5];
    return [m0, m1, m2, m3, m4, m5];
}

export default transformationMatrixReducer
