
export default class Dot{ //extends shapeTemplate{

  static addPoint(state,x,y){
    var newState = {};
    newState.coordinates = [{'x':x,'y':y}];
    newState.completeDrawing = true;
    newState.numberOfCoordinates = 1;
    return newState;
  }

  static findPoints(state){
    var newState = {};
    var xCoordinate,yCoordinate;
    newState.annotationPixels = {};
    newState.minX = state.coordinates[0].x;
    newState.maxX = state.coordinates[0].x;
    newState.minY = state.coordinates[0].y;
    newState.maxY = state.coordinates[0].y;
    
    for(var x=0;x<=Math.ceil(state.brushSize);x++){
      for(var y=0;y<=Math.ceil(state.brushSize);y++){
        xCoordinate = state.coordinates[0].x + x;
        yCoordinate = state.coordinates[0].y + y;
        newState.annotationPixels['x'+xCoordinate+'y'+yCoordinate] = 1;
        xCoordinate = state.coordinates[0].x - x;
        yCoordinate = state.coordinates[0].y - y;
        newState.annotationPixels['x'+xCoordinate+'y'+yCoordinate] = 1;
      }
    }
    return newState;
  }

  static save(state)
  {
    var newState = {};
    newState.completeDrawing = true;
    newState.editCoordinates = [];
    findPoints();
    return newState
  }

  static undo()
  {
    return false;
  }

  // static checkIfInBoundary(x,y,x1,y1){
  //   if((x-x1)<=Math.ceil(this.brushSize) && (x-x1)>=-Math.ceil(this.brushSize) && (y-y1)<=Math.ceil(this.brushSize) && (y-y1)>=-Math.ceil(this.brushSize)){
  //     this.staticDraw = true;
  //     this.completeDrawing = true;
  //   }
  // }

  static edit(state){
    return state;
  }

  static undoEdit(state)
  {
    return state;
    // if(this.editCoordinates.length>0){
    //   this.coordinates = this.editCoordinates[this.editCoordinates.length-1]
    //   this.editCoordinates.splice(this.editCoordinates.length-1,1);
    //   this.edit();
    //   return true;
    // }
    // else{
    //   this.save();
    //   return false;
    // }
  }

  static addEditPoint(state,x,y){
    var point = (x,y);
    var newState = {};
    newState.editCoordinates = state.editCoordinates
    newState.editCoordinates.push([...state.coordinates]);
    newState.coordinates = state.coordinates;
    newState.coordinates[0] = point;
    return newState
  }
  
}