import React from 'react';
import TitleName from "./Title";
import ProjectDetailsArrow from "./ProjectOverview";
import SearchBox from "../../UI/shared/SearchBox";
import MostRecent from "../../UI/shared/MostRecent";
import ProjectImageCard from "../../UI/Cards/ProjectImageCard";
import Taggers from "./Taggers";
import Testers from "./Testers";
import ProjectDetails from "./ProjectDetails";
import CompletionRate from "./CompletionRate";
import { useDispatch, useSelector } from "react-redux";


export default function Content() {
    const state = useSelector((state) => state.project);
    return (
        <div className="container p-5 mt-5 mb-5">
            <div className="">
                <div className="row">
                    <div className="col-md-8">
                        <div className="row">
                            <SearchBox />
                            <TitleName title={state.project.name} />
                            <MostRecent />
                        </div>
                    </div>
                    <div className="col-md-4">
                        <ProjectDetailsArrow />
                    </div>
                </div>
                <div className="row mb-5">
                    <div className="col-md-8 background-white rounded">
                        <div className="row" style={{ borderRadius: "10px" }}>
                            {state.images.map((image, key) => {
                                return <ProjectImageCard image={image} />;
                            })}
                        </div>
                    </div>
                    <div className="col-md-4">
                        <Taggers />
                        <Testers />
                        <CompletionRate />
                        <ProjectDetails />
                    </div>
                </div>
            </div>
        </div>

    )
}
