import {
  SET_CURRENT_IMAGE,
} from './constants'

const initialState = {
  image: {},
  segmentations:[],
  annotations:[],
  labelMapping:{},
  imageId: 0
}

const imageReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CURRENT_IMAGE:
      return {
        ...state,
        image: action.image
      }

    default: return state
  }
}

export default imageReducer
