import React from "react";
import RightBar from "../components/Tagging/RightBar";
import Sidebar from "../components/Tagging/Sidebar";
import Topbar from "../components/Tagging/Topbar";
import "../static/css/custom.css";
import { useSelector } from "react-redux";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { getWidth, getHeight } from "../Util/utilFunctions";

const TaggingPage = () => {
  const minWidth = useMediaQuery("(min-width:1260px)");
  const maxWidth = useMediaQuery("(max-width:1430px)");
  const breakPoint = minWidth && maxWidth;

  const util = useSelector((state) => state.util);
  const image = useSelector((state) => state.image);
  console.log(getHeight());

  const style1 = () => {
    return {
      position: "fixed",
      top: "7%",
      left: "5%",
      right: "0",
      bottom: "0",
      width: util.tagRightBar ? "95%" : "77%",
      height: "93%",
    };
  };

  const style2 = () => {
    return {
      position: "fixed",
      top: "07%",
      left: "8%",
      right: "0",
      bottom: "0",
      width: util.tagRightBar ? "92%" : "72%",
      height: "93%",
    };
  };

  const imgStyle = () => {
    return {
      height: `${getHeight()}px`,
      width: `${getWidth(breakPoint, util.tagRightBar)}px`,
    };
  };

  
  return (
    <div>
      <Topbar />
      <Sidebar />
      <div className="mb-5" style={breakPoint ? style2() : style1()}>
        <img
          style={imgStyle()}
          src={image.image.path}
          alt="Avatar"
          className="img-fluid"
        />
      </div>
      {util.tagRightBar ? null : <RightBar />}
    </div>
  );
};

export default TaggingPage;
