import React, { useState } from "react";
import TaggerTable from "./taggerTable";
import TesterTable from "./testerTable";

const TabPanel = () => {
  const [value, setvalue] = useState("taggers");

  const useStyle = () => {
    return {
      backgroundColor: "#161d53",
      color: "white",
    };
  };
  return (
    <div className="container">
      <div className="row justify-content-center">
        <div className="col-md-6 card rounded shadow-sm">
          <div className="row text-center" style={{ cursor: "pointer" }}>
            <div
              className="col-6 text-center p-2"
              onClick={() => setvalue("taggers")}
              style={value === "taggers" ? useStyle() : null}
            >
              Taggers
            </div>
            <div
              onClick={() => setvalue("testers")}
              className="col-6  text-center p-2"
              style={value === "testers" ? useStyle() : null}
            >
              Testers
            </div>
          </div>
        </div>
      </div>
      <div className="row mt-4">
        <div className="col-md-10 offset-md-1">
          {value === "taggers" ? <TaggerTable /> : <TesterTable />}
        </div>
      </div>
    </div>
  );
};

export default TabPanel;
