import React from "react";
import {
  PolygonFilterIcon,
  CircleIcon,
  FullCircleIcon,
  SquareIcon,
  LayoutLinearIcon,
  EyeOpenIcon,
} from "evergreen-ui";
import '../../static/css/custom.css';

const Annotations = () => {
  return (
    <div className="col-md-12 card rounded bg-white py-1 px-2 mr-1">
      <p
        className="text-dark text-center"
        style={{ fontSize: "medium", color: "black", fontFamily: "Lato" }}
      >
        Annotations
      </p>
      <table class="table table-hover mr-1">
        <tbody>
          <tr>
            <th scope="row">
              <PolygonFilterIcon color="#775ada" />
            </th>
            <td>
              <EyeOpenIcon color="red" />
            </td>
            <td>Dog-1</td>
            <td>
              <LayoutLinearIcon color="#1e2a78" />
            </td>
          </tr>
          <tr>
            <th scope="row">
              <PolygonFilterIcon color="#c94e4e" />
            </th>
            <td>
              <EyeOpenIcon color="red" />
            </td>
            <td>Human-2</td>
            <td>
              <LayoutLinearIcon color="#1e2a78" />
            </td>
          </tr>
          <tr>
            <th scope="row">
              <CircleIcon color="#facf5a" />
            </th>
            <td>
              <EyeOpenIcon color="red" />
            </td>
            <td>Human-3</td>
            <td>
              <LayoutLinearIcon color="#1e2a78" />
            </td>
          </tr>
          <tr>
            <th scope="row">
              <FullCircleIcon color="#a6acec" />
            </th>
            <td>
              <EyeOpenIcon color="red" />
            </td>
            <td>Dog-4</td>
            <td>
              <LayoutLinearIcon color="#1e2a78" />
            </td>
          </tr>
          <tr>
            <th scope="row">
              <SquareIcon color="#3d5af1" />
            </th>
            <td>
              <EyeOpenIcon color="red" />
            </td>
            <td>Human-5</td>
            <td>
              <LayoutLinearIcon color="#1e2a78" />
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default Annotations;
