import React, { useEffect } from "react";
import "../static/css/custom.css";
import { privateApi } from "../Util/API";
import { recievedProjectsAndDatasets } from "../redux/user/actions";
import { isAuthenticated } from "../Util/Auth";
import { useDispatch, useSelector } from "react-redux";
import Layout from "../UI/Layouts/Layout";
import Content from "../components/Dashboard/Content";

export default function LocuslearnDashboard() {
  const dispatch = useDispatch();
  const state = useSelector((s) => s.user);

  const handleRequest = async () => {
    let res = await privateApi(
      `/api/projects/user_all_projects/${isAuthenticated().user.user_id}/`,
      {},
      "get"
    );
    if (res.status === 200) {
      dispatch(
        recievedProjectsAndDatasets(res.body.Projects, res.body.DataSets)
      );
    }
  };

  useEffect(() => {
    if (state.datasets.length === 0) {
      handleRequest();
    }
  }, []);

  return <Layout content={Content} />;
}
