import React, { useEffect } from "react";
import TitleCount from "../../UI/shared/TitleCount";
import AttachDatasetCard from "../../UI/Cards/AttachDataSetCard";
import SearchBox from "../../UI/shared/SearchBox";
import MostRecent from "../../UI/shared/MostRecent";
import ButtonUpload from "../../UI/Buttons/Button_Upload";
import "../../static/css/custom.css";
import { useSelector, useDispatch } from "react-redux";
import UploadImages from "./UploadImages";
import { Button, ArrowRightIcon } from 'evergreen-ui'


export default function DatasetPages() {
  const currentState = useSelector((s) => s.util);
  const user = useSelector((s) => s.user);
  const dispatch = useDispatch();
  useEffect(() => {

  }, [])

  return currentState.datasetNameAdded ? (
    <UploadImages />
  ) : (
    <div className="content">
      <div className="animated fadeIn">
        <div className="container-fluid">
          <div className="col-md-9">
            <div className="row">
              <SearchBox />
              <TitleCount text={"Choose a Dataset"} num={user.datasets.length} />
              <MostRecent />
            </div>
            <div className="row">
              <div className="col-md-12">
                <div
                  className="row background-white"
                  style={{ borderRadius: "10px" }}
                >
                  {
                    user.datasets.map((dataset) => {
                      return <AttachDatasetCard dataset={dataset} />

                    })
                  }
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-3">
            <ButtonUpload />
            <br />
            <div className="col-md-12 mt-5 text-center">
              {currentState.datasets.length !== 0 ? <Button onClick={() => dispatch({ type: "ACTIVE_TAB", tab: 'label' })} marginRight={12} height={32} appearance="primary" iconAfter={ArrowRightIcon} >Next</Button> : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
