import React from "react";
import "../../static/css/custom.css";
import { Link } from "react-router-dom";
import { setCurrentProject } from "../../redux/project/actions";
import { useDispatch } from "react-redux";

const ProjectCard = (props) => {
  const dispatch = useDispatch();
  const setProject = () => {
    dispatch(setCurrentProject(props.project));
  };

  return (
    <div
      className={
        props.className === undefined
          ? "col-lg-3 col-xl-3 col-md-3"
          : props.className
      }
    >
      <Link to="/project-images">
        <div
          className="card-body image-card card-height bg mb-1 mt-2 "
          onClick={() => setProject()}
        >
          <div className="card-body card-height "></div>
        </div>
      </Link>
      <div className="row extra-row-class">
        <div className="project-data">
          <p className="project-name">
            {props.project !== undefined
              ? props.project.name.length <= 12
                ? props.project.name
                : `${props.project.name.substring(0, 12)}...`
              : "Project Name"}
          </p>
        </div>
        <div className="project-timeline ">
          <h5
            className="project-time"
            style={{ fontSize: "10px", fontWeight: "400" }}
          >
            08:52 A.M
          </h5>
          <h5
            className="project-date"
            style={{ marginTop: "0px", fontSize: "10px", fontWeight: "400" }}
          >
            Tue 07/01
          </h5>
        </div>
      </div>
    </div>
  );
};

export default ProjectCard;
