import React, { useState } from "react";
import { Tooltip } from "evergreen-ui";
import '../../static/css/custom.css';

const ToolIcon = (props) => {
  const [value, setvalue] = useState(false);
  const onSelected = () => {
    setvalue(!value);
  };
  return (
    <Tooltip content={props.name}>
      <div onClick={onSelected} style={{ cursor: "pointer" }}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          x="0px"
          y="0px"
          width="40"
          height="40"
          viewBox="0 0 172 172"
          style={{
            backgroundColor: value ? `#581b98` : "#E5E9F2",
            padding: "5px",
            borderRadius: "8px",
          }}
        >
          <g
            fill="none"
            fill-rule="nonzero"
            stroke="none"
            stroke-width="1"
            stroke-linecap="butt"
            stroke-linejoin="miter"
            stroke-miterlimit="10"
            stroke-dasharray=""
            stroke-dashoffset="0"
            font-family="none"
            font-weight="none"
            font-size="none"
            text-anchor="none"
            // style={{ mixBlendMode: "normal" }}
          >
            <path d="M0,172v-172h172v172z" fill="none"></path>
            <g fill={value ? "#E5E9F2" : "#000840"}>
              <path d={props.path}></path>
            </g>
          </g>
        </svg>
      </div>
    </Tooltip>
  );
};

export default ToolIcon;
