import {
  SET_CURRENT_DATASET,
  ADD_DATASET_DETAILS,
  ADD_DATASET_IMAGES,
  RESET_IMAGES,
} from "./constants";

let initialState = {
  dataset: {},
  images: [],
  imagesRecived: false,
};

const datasetReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CURRENT_DATASET:
      return {
        ...state,
        dataset: action.dataset,
      };

    case ADD_DATASET_DETAILS:
      return {
        dataset: {
          state,
        },
      };
    case ADD_DATASET_IMAGES:
      return {
        ...state,
        images: action.images,
        imagesRecived: true,
      };
    case RESET_IMAGES:
      return { ...state, images: [], imagesRecived: false };
    default:
      return state;
  }
};

export default datasetReducer;
