import React from "react";
import { Helmet } from "react-helmet";
import LoginForm from "../components/Login/form";
import "../static/css/custom.css";

const LocuslearnLogin = () => {
  return (
    <div className="body">
      <Helmet>
        <style>{"body { background-color: #DEDEDE; }"}</style>
      </Helmet>
      <div className="container-fluid">
        <div className="row mt-2">
          <div className="col-md-2 logo-login-page"></div>
        </div>
        <div className="row mt-5">
          <div className="container">
            <div className="row">
              <div className="col-md-6 col-xl-6 ml-2 px-1"></div>
              <div
                className="col-md-5 col-xl-5"
                style={{ verticalAlign: "middle" }}
              >
                <LoginForm />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LocuslearnLogin;
