import React from "react";
import "../../static/css/custom.css";
import { Link } from "react-router-dom";
import { useDispatch } from 'react-redux'

export default function ButtonLarge(props) {
  const dispatch = useDispatch()
  return (
    <div
      className={
        props.className === undefined ? "col-3 ml-5 mt-1" : props.className
      }
    >
      <Link to="/create-newproject">
        <div
          onClick={() => dispatch({ type: "RESET_UTIL" })}
          className="New-project-btn ml-4  p-5 w-100 rounded waves-effect text-center"
          style={{ backgroundColor: props.color }}
        >
          <a href="#">
            <h5 className="new-project">
              {props.text}
              <span className="project-icon">
                {props.icon === undefined ? null : (
                  <i class={props.icon} aria-hidden="true"></i>
                )}
              </span>
            </h5>
          </a>
        </div>
      </Link>
    </div>
  );
}
