const intialState = {
  newProject: {
    project_name: "",
    company: "",
    additional_details: "",
    industry: "",
    purpose: "",
  },
  newDatasetName: "",
  datasets: [],
  labels: [],
  tabValue: "project",
  datasetNameAdded: false,
  sidebarToggle: false,
  tagRightBar: false,
};

const utilReducer = (state = intialState, action) => {
  switch (action.type) {
    case "ACTIVE_TAB":
      return { ...state, tabValue: action.tab };
    case "SET_PROJECT":
      return { ...state, newProject: action.values };
    case "ADD_PROJECT_NAME":
      return {
        ...state,
        newProject: { ...state.newProject, project_name: action.value },
      };
    case "ENTER_DATASET_NAME":
      return { ...state, newDatasetName: action.input };
    case "DATASET_NAME_ADDED":
      return {
        ...state,
        datasetNameAdded: action.bool === undefined ? true : action.bool,
      };
    case "ADD_DATASET_ID":
      return { ...state, datasets: [...state.datasets, action.id] };
    case "REMOVE_DATASET_ID":
      var newArray = state.datasets.filter((item) => item.id != action.item);
      return { ...state, datasets: newArray };
    case "ADD_LABEL":
      return { ...state, labels: [...state.labels, action.label] };
    case "REMOVE_LABEL":
      var newArray = state.labels.filter((item) => item.label != action.label);
      return { ...state, labels: newArray };
    case "TOGGLE_SIDEBAR":
      return { ...state, sidebarToggle: action.bool };
    case "TOGGLE_TAGGING_RIGHTBAR":
      return { ...state, tagRightBar: !state.tagRightBar };
    case "RESET_UTIL":
      state = intialState;
      return state;
    default:
      return state;
  }
};

export default utilReducer;
