import React, { useState } from "react";
import "../../static/css/custom.css";

const LabelInput = () => {
  const [values, setvalues] = useState([
    "Head",
    "Dog",
    "Tree",
    "EYE",
    "Nose",
    "Forehead",
    "Throat",
    "Hand",
    "Mouth",
  ]);
  const [suggestions, setsuggestions] = useState([]);
  const [label, setlabel] = useState("");

  const onLabelChange = (e) => {
    let value = e.target.value;
    let suggested = [];
    if (value.length > 0) {
      const regex = new RegExp(`^${value}`, "i");
      suggested = values.sort().filter((v) => regex.test(v));
    }
    if (value.length == 0) {
      setsuggestions(values);
    }
    setsuggestions(suggested);
    setlabel(value);
  };

  const onSuggestionSeleted = (value) => {
    setlabel(value);
    setsuggestions([]);
  };

  const renderSuggestions = () => {
    if (suggestions.length === 0) {
      return null;
    }
    return (
      <ul style={{ position: "static" }} className="list-group">
        {suggestions.map((v, i) => {
          return (
            <li
              key={i}
              className="list-group-item text-center text-primary suggestion-box"
              onClick={() => onSuggestionSeleted(v)}
              style={{ cursor: "pointer",fontWeight:'300',fontSize:'small', }}
            >
              {v}
            </li>
          );
        })}
      </ul>
    );
  };

  const style = () => {
    return {
      backgroundColor: "transparent",
      outline: "none",
      borderLeft: "none",
      borderTop: "none",
      borderRight: "none",
      borderBorder: "1px solid white",
      width: "80%",
      fontWeight: "400",
      fontSize: "small",
    };
  };
  return (
    <div className="col-md-12 mb-4">
      <div className="text-center">
        <label
          className="text-white"
          htmlFor="label"
          style={{ fontSize: "x-small" }}
        >
          Enter Label
        </label>
      </div>
      <input
        onChange={onLabelChange}
        style={style()}
        type="text"
        name=""
        id="label"
        value={label}
        className="text-white"
        onFocus={() => setsuggestions(values)}
        onClick={() => setsuggestions(values)}
      />
      {renderSuggestions()}
    </div>
  );
};

export default LabelInput;
