import React from "react";

export default function Title(props) {
  return (
    <div className="col-md-4 text-center">
      <h5
        className="subhead"
        style={{ fontWeight: 600, fontSize: "14", color: "#000840" }}
      >
        {props.title}
        <i
          className="ml-2 fa fa-pencil-square-o color-pink"
          aria-hidden="true"
        ></i>
      </h5>
      <small style={{ color: "#2E3BF0", fontWeight: 500, fontSize: "12px" }}>
        {props.count} <span className="text-dark">Images</span>
      </small>
    </div>
  );
}
