import {
  SET_CURRENT_DATASET,
  ADD_DATASET_DETAILS,
  ADD_DATASET_IMAGES,
} from "./constants";

export function setCurrentDataset(dataset) {
  return {
    type: SET_CURRENT_DATASET,
    dataset: dataset,
  };
}

export function addDatasetDetails(datasetDetails) {
  return {
    type: ADD_DATASET_DETAILS,
    datasetDetails: datasetDetails,
  };
}

export function getAllDatasetImages(images) {
  return {
    type: ADD_DATASET_IMAGES,
    images: images,
  };
}
