import React, { useState } from "react";
import Content from "../../UI/Layouts/Content";
import ProjectSummary from "../Project-Details/ProjectSummary";
import Settings from "../Project-Details/Settings";
import LabelsDone from "../Project-Details/LabelsDoneTable";
import LabelsPending from "../Project-Details/LabelsPending";
import Performance from "../Project-Details/Performance";
import Members from "../Project-Details/Members";

const Tabs = () => {
  const [value, setvalue] = useState("project-summary");

  const style = () => {
    return {
      position: "fixed",
      top: "10%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "15%",
      height: "100%",
      backgroundColor: "#000840",
      overflowY: "auto",
    };
  };

  const expandedStyle = (noBg) => {
    return {
      position: "fixed",
      top: "10%",
      left: "15%",
      right: "0",
      bottom: "0",
      width: "85%",
      height: "100%",
      backgroundColor: noBg ? "#f5f6fa" : "rgb(0, 8, 64)",
      overflowY: "auto",
    };
  };

  const customStyle = () => {
    return {
      fontWeight: "500",
      fontSize: "15px",
      letterSpacing: "1px",
      color: "black",
      borderRadius: "3px",
      backgroundColor: "white",
    };
  };

  const noSelectedStyle = () => {
    return {
      fontWeight: "500",
      fontSize: "15px",
      color: "white",
      cursor: "pointer",
    };
  };

  const handleClick = (name) => {
    setvalue(name);
  };

  const content = (ui) => {
    return (
      <div className="" style={expandedStyle()}>
        <div className="br p-5" style={expandedStyle(true)}></div>
        {ui()}
      </div>
    );
  };

  const returnContent = () => {
    if (value === "project-summary") {
      return content(ProjectSummary);
    } else if (value === "labels-done") {
      return content(LabelsDone);
    } else if (value === "labels-pending") {
      return content(LabelsPending);
    } else if (value === "performance") {
      return content(Performance);
    } else if (value === "members") {
      return content(Members);
    } else {
      return content(Settings);
    }
  };

  const sideNav = () => {
    return (
      <div style={style()}>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={
              value === "project-summary" ? customStyle() : noSelectedStyle()
            }
            onClick={() => handleClick("project-summary")}
          >
            Project Summary
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3"
            style={value === "labels-done" ? customStyle() : noSelectedStyle()}
            onClick={() => handleClick("labels-done")}
          >
            Labels Done
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={
              value === "labels-pending" ? customStyle() : noSelectedStyle()
            }
            onClick={() => handleClick("labels-pending")}
          >
            Labels Pending
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={value === "performance" ? customStyle() : noSelectedStyle()}
            onClick={() => handleClick("performance")}
          >
            Performance
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={value === "members" ? customStyle() : noSelectedStyle()}
            onClick={() => handleClick("members")}
          >
            Members
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={value === "settings" ? customStyle() : noSelectedStyle()}
            onClick={() => handleClick("settings")}
          >
            Settings
          </div>
        </div>
      </div>
    );
  };

  return (
    <>
      {sideNav()}
      {returnContent()}
    </>
  );
};

export default Tabs;
