import React from "react";

export default function SearchBox() {
  return (
    <div className="col-md-4 ">
      <div className="search-bar-filter">
        <input
          style={{ height: "30px" }}
          id="filter"
          type="text"
          placeholder="Search"
          className="text-left"
        />
      </div>
    </div>
  );
}
