import React from "react";
import { LayoutLinearIcon } from "evergreen-ui";

const Settings = () => {
  return (
    <div
      className="tab-pane mt-5"
      id="settings"
      role="tabpanel"
      aria-labelledby="nav-contact-tab"
    >
      <div className="row">
        <div className="col-md-6 mt-5">
          <p className="heads">Project Name</p>
        </div>
      </div>
      <div className="row">
        <div className="col-md-6">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-md-4">
                  <p className="proj-settings">Project Name</p>
                </div>
                <div className="col-md-6 mt-2">
                  <p className="proj-settings-content">Name of the Project</p>
                </div>
                <div className="col-md-2">
                  <LayoutLinearIcon color="grey" />
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <p className="proj-settings">Description</p>
                </div>
                <div className="col-md-6">
                  <p className="proj-settings-content">
                    Lorem ipsum dolor sit amet, consectetuer adipiscing elit,
                    sed diam nonummy nibh
                  </p>
                </div>
                <div className="col-md-2">
                  <LayoutLinearIcon color="grey" />
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <p className="proj-settings">Annotations</p>
                </div>
                <div className="col-md-6">
                  <p className="proj-settings-content">
                    <img
                      className="icon-img-width"
                      src="static/images/polygon-img.png"
                    />
                    <img
                      className="icon-img-width"
                      src="static/images/brush-img.png"
                    />
                    <img
                      className="icon-img-width"
                      src="static/images/rectangle-img.png"
                    />
                  </p>
                </div>
                <div className="col-md-2">
                  <LayoutLinearIcon color="grey" />
                </div>
              </div>
              <div className="row">
                <div className="col-md-4">
                  <p className="proj-settings">Dataset</p>
                </div>
                <div className="col-md-6">
                  <p className="proj-settings-content">Dataset.pdf</p>
                </div>
                <div className="col-md-2">
                  <LayoutLinearIcon color="grey" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Settings;
