import React, { useState } from "react";
import ProjectDetails from "./ProjectDetails";
import DatasetPages from "./DatasetPages";
import CustomizeLabel from "./CustomiseLabel";
import { useSelector, useDispatch } from "react-redux";
import Content from "../../UI/Layouts/Content";

const Sidenav= () => {
  const currentState = useSelector((s) => s.util);
  const dispatch = useDispatch();

  const style = () => {
    return {
      position: "fixed",
      top: "10%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "15%",
      height: "100%",
      backgroundColor: "#000840",
    };
  };

  const customStyle = () => {
    return {
      fontWeight: "500",
      fontSize: "15px",
      letterSpacing: "1px",
      borderRadius: "10px",
      color: "black",
      backgroundColor: "white",
    };
  };

  const noSelectedStyle = () => {
    return {
      fontWeight: "500",
      fontSize: "15px",
      color: "white",
    };
  };

  const sideNav = () => {
    return (
      <div style={style()}>
        <div className="mt-5 py-3 text-center">
          <p
            className="mt-3"
            style={{ fontSize: "14px", fontWeight: "400", color: "#FB29FD" }}
          >
            Create a project
          </p>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={
              currentState.tabValue === "project"
                ? customStyle()
                : noSelectedStyle()
            }
            onClick={() => dispatch({ type: "ACTIVE_TAB", tab: "project" })}
          >
            Project Details
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3"
            style={
              currentState.tabValue === "dataset"
                ? customStyle()
                : noSelectedStyle()
            }
            onClick={() =>
              currentState.newProject.project_name === ""
                ? null
                : dispatch({ type: "ACTIVE_TAB", tab: "dataset" })
            }
          >
            Attach Dataset
          </div>
        </div>
        <div className="row mt-5">
          <div
            className="col-md-8 offset-md-2 text-center p-3 "
            style={
              currentState.tabValue === "label"
                ? customStyle()
                : noSelectedStyle()
            }
            onClick={() => currentState.datasets.length===0?null:dispatch({ type: "ACTIVE_TAB", tab: "label" })}
          >
            Customise Label Editor
          </div>
        </div>
      </div>
    );
  };

  return (
    <>
      {sideNav()}
      {currentState.tabValue === "project" ? (
        <Content content={ProjectDetails} />
      ) : currentState.tabValue === "dataset" ? (
        <Content content={DatasetPages} />
      ) : (
        <Content content={CustomizeLabel} />
      )}
    </>
  );
};

export default Sidenav;
