import React from "react";
import { useSelector } from "react-redux";
import "../../static/css/custom.css";

const Content = (props) => {
  const util = useSelector((state) => state.util);
  const expandedStyle = (noBg) => {
    return {
      position: "fixed",
      top: "10%",
      left: "15%",
      right: "0",
      bottom: "0",
      width: "85%",
      height: "100%",
      backgroundColor: noBg ? "#f5f6fa" : "rgb(0, 8, 64)",
      overflowY: "auto",
    };
  };

  const shrinkStyle = (noBg) => {
    return {
      position: "fixed",
      top: "10%",
      left: "5%",
      right: "0",
      bottom: "0",
      width: "95%",
      height: "100%",
      backgroundColor: noBg ? "#f5f6fa" : "rgb(0, 8, 64)",
      overflowY: "auto",
    };
  };

  return (
    <div
      className="p-3"
      style={util.sidebarToggle ? shrinkStyle() : expandedStyle()}
    >
      <div
        className="br p-3"
        style={util.sidebarToggle ? shrinkStyle(true) : expandedStyle(true)}
      >
        <props.content />
      </div>
    </div>
  );
};

export default Content;
