import React from "react";
import { Avatar } from "evergreen-ui";

const Form = () => {
  return (
    <div className="mt-5 ">
      <div className="row mt-5">
        <div className="col-md-10 offset-md-1 ">
          <div className="row">
            <div className="col-md-6 ">
              <div className="row mt-5 mb-5">
                <div className="col-6 offset-3 text-center">
                  <Avatar
                    size={85}
                    backgroundColor="#dee1ec"
                    name="surendra vadaparthy"
                  />
                  <h5
                    className="signup-profile-text"
                    style={{ fontWeight: "400", fontSize: "14px" }}
                  >
                    Update Profile Picture
                    <br />
                    <span className="signup-remove-txt">Remove</span>
                  </h5>
                </div>
              </div>
              <div className="col-12">
                <div className="form-group">
                  <label className="label-login-page">Full Name</label>
                  <input
                    type="text"
                    className="form-control form-control-line"
                  />
                  <i
                    class="fa fa-pencil-square-o icon-align icon-color"
                    aria-hidden="true"
                  ></i>
                </div>
              </div>
              <div className="col-12">
                <div className="form-group">
                  <label className="label-login-page">Email / Username</label>
                  <input
                    type="text"
                    className="form-control form-control-line"
                  />
                  <i
                    class="fa fa-pencil-square-o icon-align icon-color"
                    aria-hidden="true"
                  ></i>
                </div>
              </div>
              <div className="col-12">
                <div className="form-group">
                  <label className="label-login-page">Password</label>
                  <input
                    type="text"
                    className="form-control form-control-line"
                  />
                  <i
                    class="fa fa-pencil-square-o icon-align icon-color"
                    aria-hidden="true"
                  ></i>
                </div>
              </div>
              <div className="col-12">
                <div className="form-group">
                  <label className="label-login-page">Phone number</label>
                  <input
                    type="text"
                    className="form-control form-control-line"
                  />
                  <i
                    class="fa fa-pencil-square-o icon-align icon-color"
                    aria-hidden="true"
                  ></i>
                </div>
              </div>
            </div>
          </div>
          <br />
          <br />

          <div className="row mt-5">
            <div className="col-6 ">
              <button
                type="button"
                style={{
                  fontSize: "medium",
                  boxShadow: "0px 0px 0px 0px",
                  backgroundColor: "#4216d2",
                }}
                className=" btn adm-pro-submit btn-block rounded "
              >
                Update Profile
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Form;
