import React, { useState } from "react";
import Annotations from "./Annotations";
import Classes from "./Classes";
import "../../static/css/custom.css";
import { ChevronDownIcon, ChevronUpIcon, TickCircleIcon } from "evergreen-ui";
import useMediaQuery from "@material-ui/core/useMediaQuery";


const RightBar = () => {
  const minWidth = useMediaQuery("(min-width:1260px)");
  const maxWidth = useMediaQuery("(max-width:1430px)");
  const breakPoint = minWidth && maxWidth;
  const [value, setvalue] = useState("taggers");
  const [value2, setvalue2] = useState(false);

  const useStyle = () => {
    return {
      backgroundColor: "#161d53",
      color: "white",
    };
  };
  const style1 = () => {
    return {
      position: "fixed",
      top: "7%",
      left: "82%",
      right: "0",
      bottom: "0",
      width: "18%",
      height: "100%",
      backgroundColor: "white",
      overflowY: "scroll",
      overflowX: "hidden",
    };
  };
  const style2 = () => {
    return {
      position: "fixed",
      top: "7%",
      left: "80%",
      right: "0",
      bottom: "0",
      width: "20%",
      height: "100%",
      backgroundColor: "white",
      overflowY: "scroll",
      overflowX: "hidden",
    };
  };

  return (
    <div style={breakPoint ? style2() : style1()} className="p-1 mb-5">
      {value2 ? null : (
        <div className="row p-5 ">
          <div className="col-md-12 card shadow-sm rounded">
            <div
              className="row  py-3"
              style={{ fontSize: "x-small", color: "#707070" }}
            >
              <div className="col-md-6 text-center">
                <span className="mr-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    x="0px"
                    y="0px"
                    width="15"
                    height="15"
                    viewBox="0 0 172 172"
                    style={{ fill: "#000000" }}
                  >
                    <g
                      fill="none"
                      fill-rule="nonzero"
                      stroke="none"
                      stroke-width="1"
                      stroke-linecap="butt"
                      stroke-linejoin="miter"
                      stroke-miterlimit="10"
                      stroke-dasharray=""
                      stroke-dashoffset="0"
                      font-family="none"
                      font-weight="none"
                      font-size="none"
                      text-anchor="none"
                      style={{ mixBlendMode: " normal" }}
                    >
                      <path d="M0,172v-172h172v172z" fill="none"></path>
                      <g fill="#4216d2">
                        <path d="M35.83333,21.5c-7.91917,0 -14.33333,6.41417 -14.33333,14.33333v100.33333c0,7.91917 6.41417,14.33333 14.33333,14.33333h100.33333c7.91917,0 14.33333,-6.41417 14.33333,-14.33333v-100.33333c0,-7.91917 -6.41417,-14.33333 -14.33333,-14.33333zM86,38.29688c1.83377,0 3.66956,0.70211 5.06706,2.09961l9.77018,9.77018h13.82943c3.956,0 7.16667,3.21067 7.16667,7.16667v13.82943l9.77018,9.77018c2.80217,2.80217 2.80217,7.33911 0,10.13411l-9.77018,9.77018v13.82943c0,3.956 -3.21067,7.16667 -7.16667,7.16667h-13.82943l-9.77018,9.77018c-2.80217,2.795 -7.33911,2.795 -10.13411,0l-9.77018,-9.77018h-13.82943c-3.956,0 -7.16667,-3.21067 -7.16667,-7.16667v-13.82943l-9.77018,-9.77018c-2.80217,-2.80217 -2.80217,-7.33911 0,-10.13411l9.77018,-9.77018v-13.82943c0,-3.956 3.21067,-7.16667 7.16667,-7.16667h13.82943l9.77018,-9.77018c1.40108,-1.3975 3.23328,-2.09961 5.06706,-2.09961zM86,57.33333l-7.16667,7.16667h-14.33333v14.33333l-7.16667,7.16667l7.16667,7.16667v14.33333h14.33333l7.16667,7.16667z"></path>
                      </g>
                    </g>
                  </svg>
                </span>
                Brightness
              </div>
              <div className="col-md-6 text-center">
                <span className="mr-1">
                  {" "}
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    x="0px"
                    y="0px"
                    width="15"
                    height="15"
                    viewBox="0 0 172 172"
                    style={{ fill: "#000000" }}
                  >
                    <g
                      fill="none"
                      fill-rule="nonzero"
                      stroke="none"
                      stroke-width="1"
                      stroke-linecap="butt"
                      stroke-linejoin="miter"
                      stroke-miterlimit="10"
                      stroke-dasharray=""
                      stroke-dashoffset="0"
                      font-family="none"
                      font-weight="none"
                      font-size="none"
                      text-anchor="none"
                      style={{ mixBlendMode: " normal" }}
                    >
                      <path d="M0,172v-172h172v172z" fill="none"></path>
                      <g fill="#4216d2">
                        <path d="M86,14.33333c-39.49552,0 -71.66667,32.17115 -71.66667,71.66667c0,39.49552 32.17115,71.66667 71.66667,71.66667c39.49552,0 71.66667,-32.17115 71.66667,-71.66667c0,-39.49552 -32.17115,-71.66667 -71.66667,-71.66667zM86,28.66667c31.74921,0 57.33333,25.58412 57.33333,57.33333c0,31.74921 -25.58412,57.33333 -57.33333,57.33333z"></path>
                      </g>
                    </g>
                  </svg>
                </span>{" "}
                Contrast{" "}
                <span
                  style={{ cursor: "pointer" }}
                  onClick={() => setvalue2(true)}
                >
                  <ChevronDownIcon />
                </span>
              </div>
            </div>
          </div>
        </div>
      )}
      {value2 ? (
        <div className="card rounded bg-white mt-3">
          <div className="row">
            <div className="col-12">
              <div
                style={{ cursor: "pointer" }}
                onClick={() => setvalue2(false)}
                className="d-flex justify-content-end mr-3 text-right"
              >
                <ChevronUpIcon />
              </div>
              <div className="row mb-2 mt-2">
                <div className="col-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    x="0px"
                    y="0px"
                    width="20"
                    height="20"
                    viewBox="0 0 172 172"
                    style={{ fill: "#000000" }}
                  >
                    <g
                      fill="none"
                      fill-rule="nonzero"
                      stroke="none"
                      stroke-width="1"
                      stroke-linecap="butt"
                      stroke-linejoin="miter"
                      stroke-miterlimit="10"
                      stroke-dasharray=""
                      stroke-dashoffset="0"
                      font-family="none"
                      font-weight="none"
                      font-size="none"
                      text-anchor="none"
                      style={{ mixBlendMode: " normal" }}
                    >
                      <path d="M0,172v-172h172v172z" fill="none"></path>
                      <g fill="#4216d2">
                        <path d="M35.83333,21.5c-7.91917,0 -14.33333,6.41417 -14.33333,14.33333v100.33333c0,7.91917 6.41417,14.33333 14.33333,14.33333h100.33333c7.91917,0 14.33333,-6.41417 14.33333,-14.33333v-100.33333c0,-7.91917 -6.41417,-14.33333 -14.33333,-14.33333zM86,38.29688c1.83377,0 3.66956,0.70211 5.06706,2.09961l9.77018,9.77018h13.82943c3.956,0 7.16667,3.21067 7.16667,7.16667v13.82943l9.77018,9.77018c2.80217,2.80217 2.80217,7.33911 0,10.13411l-9.77018,9.77018v13.82943c0,3.956 -3.21067,7.16667 -7.16667,7.16667h-13.82943l-9.77018,9.77018c-2.80217,2.795 -7.33911,2.795 -10.13411,0l-9.77018,-9.77018h-13.82943c-3.956,0 -7.16667,-3.21067 -7.16667,-7.16667v-13.82943l-9.77018,-9.77018c-2.80217,-2.80217 -2.80217,-7.33911 0,-10.13411l9.77018,-9.77018v-13.82943c0,-3.956 3.21067,-7.16667 7.16667,-7.16667h13.82943l9.77018,-9.77018c1.40108,-1.3975 3.23328,-2.09961 5.06706,-2.09961zM86,57.33333l-7.16667,7.16667h-14.33333v14.33333l-7.16667,7.16667l7.16667,7.16667v14.33333h14.33333l7.16667,7.16667z"></path>
                      </g>
                    </g>
                  </svg>
                </div>
                <div className="col-10 mt-1">
                  <input type="range" step={1} min="0" max="40" />
                </div>
              </div>
              <div className="row">
                <div className="col-1">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    x="0px"
                    y="0px"
                    width="20"
                    height="20"
                    viewBox="0 0 172 172"
                    style={{ fill: "#000000" }}
                  >
                    <g
                      fill="none"
                      fill-rule="nonzero"
                      stroke="none"
                      stroke-width="1"
                      stroke-linecap="butt"
                      stroke-linejoin="miter"
                      stroke-miterlimit="10"
                      stroke-dasharray=""
                      stroke-dashoffset="0"
                      font-family="none"
                      font-weight="none"
                      font-size="none"
                      text-anchor="none"
                      style={{ mixBlendMode: " normal" }}
                    >
                      <path d="M0,172v-172h172v172z" fill="none"></path>
                      <g fill="#4216d2">
                        <path d="M86,14.33333c-39.49552,0 -71.66667,32.17115 -71.66667,71.66667c0,39.49552 32.17115,71.66667 71.66667,71.66667c39.49552,0 71.66667,-32.17115 71.66667,-71.66667c0,-39.49552 -32.17115,-71.66667 -71.66667,-71.66667zM86,28.66667c31.74921,0 57.33333,25.58412 57.33333,57.33333c0,31.74921 -25.58412,57.33333 -57.33333,57.33333z"></path>
                      </g>
                    </g>
                  </svg>
                </div>
                <div className="col-10 mt-1">
                  <input
                    style={{ backgroundColor: "#fc5c9c" }}
                    step={1}
                    type="range"
                    min="0"
                    max="40"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : null}
      <div className="col-12 mb-2 mt-5">
        <div
          className="row d-flex justify-content-between"
          style={{ fontSize: "small" }}
        >
          <div
            className="col-md-5 col-sm-5 card rounded shadow-sm text-center p-2"
            onClick={() => setvalue("taggers")}
            style={value === "taggers" ? useStyle() : { cursor: "pointer" }}
          >
            Classes
          </div>
          <div
            onClick={() => setvalue("testers")}
            className="col-md-5 col-sm-5 card rounded shadow-sm text-center p-2"
            style={value === "testers" ? useStyle() : { cursor: "pointer" }}
          >
            Annotations
          </div>
        </div>
      </div>

      {value !== "taggers" ? <Annotations /> : <Classes />}

      <div className="mb-5 text-center">
        <button className="reset-btn px-4 py-1">RESET </button>
        <button className=" reset-btn px-4 py-1 ml-2 mt-5 mb-5">SKIP</button>
      </div>
      <div className="text-center mb-5 mt-5 py-5">
        <button className="complete-btn px-5 py-2 mb-5">
          Complete{" "}
          <span>
            <TickCircleIcon marginBottom={4} />
          </span>
        </button>
      </div>
    </div>
  );
};

export default RightBar;
