import React from "react";

const LabelsPending = () => {
  const TableRow = () => {
    return (
      <tr style={{ fontWeight: "300", fontSize: "13px" }}>
        <td>1</td>
        <td>25</td>
        <td>10-02-2020</td>
        <td>Tagger Name</td>
        <td className="text-danger">Pending</td>
        <td>Tagger Name</td>
        <td>00:00:00</td>
        <td>
          <i
            className="fa fa-thumbs-up"
            aria-hidden="true"
            style={{ color: "#BBBBBB" }}
          ></i>
          &nbsp;
          <i
            className="fa fa-thumbs-down"
            aria-hidden="true"
            style={{ color: "#BBBBBB" }}
          ></i>
        </td>
        <td>
          <input type="submit" value="View" className="viewbt" name="" />
        </td>
      </tr>
    );
  };

  return (
    <div
      className="tab-pane mt-5"
      id="pending"
      role="tabpanel"
      aria-labelledby="nav-contact-tab"
    >
      <div className="row">
        <div className="col-md-10 offset-md-1 mt-5">
          <div className="row">
            <div className="col-md-3">
              <p className="heads mt-4 ">Project Name</p>
            </div>
            <div className="col-md-5"></div>
            <div className="col-md-4">
              <div className="search-bar-filter">
                <i id="filtersubmit" className="fa fa-search"></i>
                <input id="filter" type="text" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="row">
        <div className="col-md-10 offset-md-1">
          <div className="card shadow-sm">
            <div className="card-body">
              <div className="row">
                <div className="col-md-12">
                  <table className="table">
                    <thead>
                      <tr  style={{ fontWeight: "400" }}>
                        <th className="proj-detail-size">S No.</th>
                        <th className="proj-detail-size">Asset ID</th>
                        <th className="proj-detail-size">Date Created</th>
                        <th className="proj-detail-size">Tagged by</th>
                        <th className="proj-detail-size">Status</th>
                        <th className="proj-detail-size">Assigned to</th>
                        <th className="proj-detail-size">Duration</th>
                        <th className="proj-detail-size">Review</th>
                        <th className="proj-detail-size"></th>
                      </tr>
                    </thead>
                    <tbody>
                      {TableRow()}
                      {TableRow()}
                      {TableRow()}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LabelsPending;
