import {
  SET_CURRENT_PROJECT,
  LOAD_IMAGES,
  RECIEVED_IMAGES,
  ADD_PROJECT_DETAILS, 
  ATTACH_DATASETS, 
  ADD_LABELS
} from './constants'


export function setCurrentProject(project){
  return {
    type: SET_CURRENT_PROJECT,
    project: project
  }
}

export function loadImages(){
  return {
    type: LOAD_IMAGES,
  }
}

export function recievedImages(images){
  return {
    type: RECIEVED_IMAGES,
    images: images
  }
}

export function addProjectDetails(projectDetails){
  return {
    type: ADD_PROJECT_DETAILS,
    projectDetails : projectDetails
  }
}


export function attachDatasets(datasets){
  return {
    type: ATTACH_DATASETS,
    datasets: datasets
  }
}


export function addLabels(labels){
  return {
    type: ADD_LABELS,
    labels : labels
  }
}
