import React, { useEffect } from "react";
import { privateApi } from "../Util/API";
import { useDispatch } from "react-redux";
import { addCollaborator } from "../redux/user/actions";
import Layout from "../UI/Layouts/Layout";
import Content from '../components/Admin-Collab/Content';

const LocuslearnAdminCollaborators = () => {
  const dispatch = useDispatch();

  const handleRequest = async () => {
    let res = await privateApi("/api/", {}, "get");
    if (res.status === 200) {
      dispatch(addCollaborator(res.body));
      return;
    }
  };

  useEffect(() => {
    handleRequest;
  }, []);

  return (
    <Layout content={Content}  />
  );
};

export default LocuslearnAdminCollaborators;
