import React from "react";
import "../../static/css/custom.css";

export default function LogoBox(props) {
  return (
    <div className="row mt-2">
      <div
        className={
          props.className === undefined
            ? "col-md-2 logo-login-page"
            : props.className
        }
      ></div>
    </div>
  );
}
