import React from "react";
import SearchBox from "../../UI/shared/SearchBox";
import MostRecent from "../../UI/shared/MostRecent";
import TitleCount from "../../UI/shared/TitleCount";
import DatasetCard from "../../UI/Cards/DatasetCard";
import ButtonLarge from "../../UI/Buttons/Button_Lg";
import "../../static/css/custom.css";
import { useSelector } from "react-redux";

export default function Content() {
  const state = useSelector((s) => s.user);
  return (
    <div className="container mt-5 py-5">
      <div className="row mb-2">
        <div class="col-md-9 ml-1">
          <div className="row ">
            <SearchBox />
            <TitleCount text={"All Datasets"} num={state.datasets.length} />
            <MostRecent />
          </div>
        </div>

        <br />
      </div>
      <div className="row ">
        <div className="col-md-9 bg-white" style={{ borderRadius: "10px" }}>
          <div className="row mt-2 ml-1 mr-1 background-white">
            {state.datasets.map((dataset, index) => {
              return <DatasetCard dataset={dataset} />;
            })}
          </div>
        </div>
        <div className="col-md-3">
          <ButtonLarge
            className="col-md-12 text-center"
            text="Add new dataset"
            icon="fa fa-plus-circle"
          />
        </div>
      </div>
    </div>
  );
}
