import S3FileUpload from "react-s3";
import { privateApi } from "../Util/API";
import { isAuthenticated } from "../Util/Auth";

export const handleS3 = async (files, datasetName) => {
  try {
    var today = new Date();
    var todayR =
      today.getFullYear() +
      "-" +
      (today.getMonth() + 1) +
      "-" +
      today.getDate();
    var directoryName = `images${datasetName}${
      isAuthenticated().user.user_id
    }${todayR}`;
    const config = {
      bucketName: "brightcomgroup",
      dirName: directoryName,
      region: "us-east-2",
      accessKeyId: "AKIATRSBRHSKHQEUPUW3",
      secretAccessKey: "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",
    };
    var counter = 0;
    for (var it = 0; it < files.length; it++) {
      const file = files[it];
      await S3FileUpload.uploadFile(file, config)
        .then((res) => {
          counter++;
        })
        .catch((err) => console.error(err));
    }

    if (counter === files.length) {
      let res = await privateApi(
        "/api/images/createdatasource/",
        {
          Dataset_Link: directoryName,
          Images_Number: counter,
          userId: isAuthenticated().user.user_id,
          Dataset_Name: datasetName,
        },
        "post"
      );
      if (res.status === 200 || res.status === 204) {
        return res;
      }
      return false;
    }
  } catch (error) {
    if (error) {
      console.log(error);
    }
  }
};
