export const isAuthenticated = () => {
  try {
    if (localStorage.getItem("state")) {
      var data = JSON.parse(localStorage.getItem("state"));
      var user = data.user;
      if (Object.keys(user.user).length === 0) {
        return false;
      }
      return user.user;
    } else {
      return false;
    }
  } catch (error) {
    return false;
  }
};
