import {
    SETUP_CANVAS,
    SET_WIDTH_HEIGHT,
    SET_ZOOM,
    SET_CONTROLPRESS,
    CHANGE_LABEL,
    SET_CANVAS_STATE,
    SET_TRANSLATE_AMOUNT,
    SET_CURRENT_ANNOTATION,
    UPDATE_TIME,
    INCREMENT_TIME,
    TOGGLE_PAUSE_ANNOTATION,
    RESET_ANNOTATIONS,
    INITIALIZE_CANVAS,
    SAVE_ANNOTATION,
    SET_STATIC_CANVAS,
    EDIT_ANNOTATION,
    SET_ANNOTATIONS,
    UPDATE_TRANSFORMATIONMATRIX
} from './constants'

// import { CANVAS_STATES } from '../../components/Containers/image/canvasStates';

const initialState = {
    width: 0,
    height: 0,
    counter: 0, // Number of tags of that type
    zoomedAmount: 0,
    annotations: [],
    segmentations: [],
    labelMapping: {},
    labels: [],
    currentAnnotation: null,
    editId: null,
    label: null,
    labelId: null,
    canvasState: null,
    time: 0,
    zoomIntensity: 0.1,
    scale: 1,
    zoomArray: [],
    originx: 0,
    originy: 0,
    translateX: 0,
    translateY: 0,
    brushSize: 2.5,
    controlPress: false,
    hideAnnotations: false,
    autoIncrementLabels: false,
    bulkTag: false,
    isToggleOn: true,
    isLoading: false,
    transformationMatrix: [1,0,0,1,0,0],
}



const canvasReducer = (state = initialState, action) => {
    switch (action.type) {
        case SETUP_CANVAS:
            return {
                ...state,
                transformationMatrix: [1,0,0,1,0,0]

            }
        case SET_WIDTH_HEIGHT:
            return {
                ...state,
                width: action.width,
                height: action.height
            }
        case UPDATE_TIME:
            return {
                ...state,
                time: action.time
            }
        case INCREMENT_TIME:
                return {
                    ...state,
                    time: state.time + 1
                }
        case SET_CONTROLPRESS:
            return {
                ...state,
                controlPress: action.controlPress
            }
        case CHANGE_LABEL:
            return {
                ...state,
                labelId: action.labelId,
                label: aciton.label,
                canvasState: CANVAS_STATES.DRAW,
            }
        case SET_CANVAS_STATE:
            return {
                ...state,
                canvasState: action.canvasState
            }

        case SET_TRANSLATE_AMOUNT:
            return {
                ...state,
                translateX: action.translateX,
                translateY: action.translateY
            }
        case SET_ZOOM:
            return {
                ...state,
                originx: action.originx,
                originy: action.originy,
                scale: action.scale,
                brushSize: action.brushSize
            }
        case RESET_ANNOTATIONS:
            return {
                ...state,
                counter: 0,
                annotations: [],
                segmentations: [],
                currentAnnotation: null,
                editId: null,
                label: null,
                labelMapping:{},
                canvasState: CANVAS_STATES.STATIC,
            }

        case TOGGLE_PAUSE_ANNOTATION:
            return{
                ...state,
                isToggle: !state.isToggle,
                currentAnnotation: null,
                editId: null,
                label: null,
                canvasState: action.canvasState,
            }

        case INITIALIZE_CANVAS:
            return{
                ...state,
                zoomedAmount: 0,
                annotations: [],
                labelMapping: {},
                brushSize: 2.5,
                currentAnnotation: null,
                editId: null,
                label: null,
                time: 0,
                scale: 1,
                zoomArray: [],
                originx: 0,
                originy: 0,
                translateX: 0,
                translateY: 0,
                image: null,
                imageId: imageId,
                imageList,
                counter: 0,
                canvasState: CANVAS_STATES.LOAD,
                time: 0
            }
        
        case SET_CURRENT_ANNOTATION:
            return {
                ...state,
                currentAnnotation: action.currentAnnotation
            }
            
        case SET_STATIC_CANVAS:
            return{
                ...state,
                currentAnnotation: null,
                editId: null,
                label: null,
                canvasState: CANVAS_STATES.STATIC
            }
        
        case SAVE_ANNOTATION:
            return{
                ...state,
            counter: action.counter,
            annotations : action.annotations,
            labelMapping :action.labelMapping,
            labelId: action.labelId,
            label: action.label,
            canvasState: action.canvasState,
            currentAnnotation: null,
            editId: null,
            }

        case EDIT_ANNOTATION:
            return{
                ...state,
            editId: action.editId,
            label: action.label,
            currentAnnotation: action.currentAnnotation,
            canvasState: CANVAS_STATES.EDIT,
            }
        case SET_ANNOTATIONS: 
            return {
                ...state,
                annotations: action.annotations,
                labelMapping: action.labelMapping
            }
        case UPDATE_TRANSFORMATIONMATRIX: 
            return {
                ...state,
                transformationMatrix: action.transformationMatrix
            }
        default: return state
    }
}

export default canvasReducer
