import React from "react";

export default function MostRecent() {
  return (
    <div className="col-md-4 px-1">
    <div
      className="card rounded py-3 px-5"
      style={{ boxShadow: "0px 0px 0px 0px", backgroundColor: "#e2f3f5" }}
    >
      <small className="most-recent">
        <i className="fa fa-arrows-v color-pink mr-1" aria-hidden="true"></i>
        &nbsp;Most Recent
      </small>
    </div>
    </div>
  );
}
