import React, { useState } from "react";
import { DropzoneArea } from "material-ui-dropzone";
import FilterDramaIcon from "@material-ui/icons/FilterDrama";
import BeatLoader from "react-spinners/BeatLoader";
import { handleS3 } from "../../config/AWS_S3_CONFIG";
import { useDispatch, useSelector } from "react-redux";
import { recievedProjectsAndDatasets } from "../../redux/user/actions";
import { privateApi } from "../../Util/API";
import { isAuthenticated } from "../../Util/Auth";

export default function UploadImages(props) {
  const [filesCount, setfilesCount] = useState(0);
  const [uploadStarted, setuploadStarted] = useState(false);
  const [status, setstatus] = useState(0);
  const [files, setfiles] = useState([]);
  const dispatch = useDispatch();
  const state = useSelector((state) => state.util);
  const handleFiles = (files) => {
    setfilesCount(files.length);
    setfiles(files);
  };

  const handleRequest = async () => {
    let res = await handleS3(files, state.newDatasetName);
    console.log(res);
    if (res.status === 200 || res.status === 204) {
      let res2 = await privateApi(
        `/api/projects/user_all_projects/${isAuthenticated().user.user_id}/`,
        {},
        "get"
      );
      if (res2.status === 200 || res2.status === 204) {
        dispatch(
          recievedProjectsAndDatasets(res2.body.Projects, res2.body.DataSets)
        );
        dispatch({ type: "DATASET_NAME_ADDED", bool: false });
      }
    }
  };

  return (
    <div className="content">
      <div className="animated fadeIn">
        <div className="container mt-5 ">
          <div className="col-md-5">
            <label className="text-dark ml-1">Enter Dataset Name</label>
            <div className="search-bar-filter ml-1">
              <input
                onChange={(e) =>
                  dispatch({
                    type: "ENTER_DATASET_NAME",
                    input: e.target.value,
                  })
                }
                style={{ height: "30px" }}
                id="filter"
                type="text"
                value={state.newDatasetName}
                placeholder="Dataset Name"
                className="text-left"
              />
            </div>
          </div>

          {uploadStarted ? (
            <div className="col-md-12 py-5 mt-5 text-center">
              <center>
                <BeatLoader size={80} color="#a6acec" />
              </center>
            </div>
          ) : (
            <div
              className="col-md-12 mt-5"
              style={
                state.newDatasetName.length === 0
                  ? { pointerEvents: "none", opacity: 0.4 }
                  : {}
              }
            >
              <DropzoneArea
                Icon={FilterDramaIcon}
                filesLimit={100}
                showPreviews={false}
                showPreviewsInDropzone={false}
                previewText="Selected files"
                onChange={(files) => handleFiles(files)}
                disableRejectionFeedback={true}
              />
              <div className="col-md-2 bg-white text-dark text-center p-4 mt-5 offset-md-5 text-center">
                Selected Images : {filesCount}
              </div>
            </div>
          )}
          {filesCount !== 0 && !uploadStarted ? (
            <div className="col-md-2 offset-md-5 mt-5 py-5">
              <button
                className="btn btn-block rounded "
                style={{ backgroundColor: "#4216d2", fontSize: "medium" }}
                onClick={handleRequest}
              >
                Upload Images
              </button>
            </div>
          ) : null}
        </div>
      </div>
    </div>
  );
}
