import React from "react";
import { Link } from "react-router-dom";
import "../../static/css/custom.css";
import { useDispatch } from "react-redux";
import { SET_CURRENT_IMAGE } from "../../redux/image/constants";

export default function ProjectImageCard(props) {
  const dispatch = useDispatch();

  let name = props.image.file_path.split("/");
  let image = props.image.path;
  const style1 = () => {
    return {
      backgroundImage: "url" + "(" + image + ")",
      backgroundSize: "cover",
      backgroundRepeat: "no-repeat",
      backgroundPosition: "center",
    };
  };
  return (
    <div
      className={
        props.className === undefined
          ? "col-lg-3 col-xl-3 col-md-3"
          : props.className
      }
      onClick={() => dispatch({ type: SET_CURRENT_IMAGE, image: props.image })}
    >
      <Link to="/tagging">
        <div
          className="card-body image-card card-height  bg mb-2"
          style={style1()}
        >
          <div className="card-body card-height"></div>
        </div>
      </Link>
      <div className="row extra-row-class">
        <div className="project-data">
          <p className="project-name">
            {props.image !== undefined
              ? name[1].length <= 12
                ? name[1]
                : `${name[1].substring(0, 12)}...`
              : "Image Name"}
            <br />
            <p className="total-labels">
              Total Labels{" "}
              <span className="">
                ({JSON.parse(props.image.labels.length)})
              </span>
            </p>
          </p>
        </div>
      </div>
    </div>
  );
}

<div className="card"></div>;
