import React from "react";
import { Link, useHistory } from "react-router-dom";
import adminImage from "../../static/images/Admin-img.png";
import { useDispatch } from "react-redux";

const Topbar = () => {
  const highLightPath = (path) => {
    if (window.location.pathname === path) {
      return {
        borderBottom: "2px solid rgb(251, 41, 253)",
        marginTop: "5px",
      };
    }
    return {};
  };
  const style1 = () => {
    return {
      position: "fixed",
      top: "0",
      left: "0",
      right: "0",
      bottom: "0",
      width: "100%",
      height: "10%",
      backgroundColor: "#000840",
    };
  };
  const dispatch = useDispatch();
  const history = useHistory();

  const handleLogout = (e) => {
    e.preventDefault();
    dispatch({ type: "RESET_UTIL" });
    dispatch({ type: "RESET_USER" });
    localStorage.clear();
    history.push("/login");
  };

  return (
    <div style={style1()}>
      <div className="row ">
        <div className="col-md-2 logo-alignment">
          <div className="logo-login-page"></div>
        </div>
        <div className="col-md-6 text-center links-alignment">
          <div className="row ">
            <div className="col-3">
              <Link to="/dashboard" style={highLightPath("/dashboard")}>
                Dashoard
              </Link>
            </div>
            <div className="col-3">
              <Link style={highLightPath("/all-projects")} to="/all-projects">
                Projects
              </Link>
            </div>
            <div className="col-3">
              <Link style={highLightPath("/all-datasets")} to="/all-datasets">
                Datasets
              </Link>
            </div>
            <div className="col-3">
              <Link to="/">Tasks</Link>
            </div>
          </div>
        </div>
        <div className="col-md-3 admin-alignment">
          <div className="pull-right d-flex justify-content-end">
            <img className="admin-img " src={adminImage} />

            <span to="/admin-profile" class="ad-txt">
              <Link style={{ color: "#fb29fd" }} to="/admin-profile">
                Admin&nbsp;
                <i class="fa fa-caret-down" aria-hidden="true"></i>
              </Link>
            </span>
            <div className="col-md-1 py-2 text-center">
              <button
                onClick={handleLogout}
                className="btn btn-logout ml-5"
                style={{ marginTop: "-22px" }}
              >
                Log Out
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Topbar;
