import React from "react";

import { CircularProgressbar, buildStyles } from "react-circular-progressbar";

export default function CompletionRate() {
  return (
    <div className="row">
      <div className="card card-realign mar-20-b-rad">
        <div className="card-body">
          <div className="row">
            <div className="col-md-6">
              <h5 className="heads">Completion Rate</h5>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6 col-sm-6">
              <CircularProgressbar
                styles={buildStyles({
                  textColor: "#FB29FD",
                  pathColor: "#4072EE",
                  trailColor: "#DEDEDE",
                  textSize: "14px",
                })}
                value={30}
                text={`${30}%`}
                strokeWidth={5}
              />
            </div>
            <div className="col-md-6">
              <div className="row py-2">
                <div className="col-md-12 py-2">
                  <h5 className="taggers-name">Total Time Taken</h5>
                  <p className="sub2">01:52:48</p>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12 py-1">
                  <h5 className="taggers-name">Avg. Time per Image</h5>
                  <p className="sub2">00:02:45</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
