import { combineReducers } from "redux";
import userReducer from "./user/reducers";
import projectReducer from "./project/reducers";
import datasetReducer from "./dataset/reducers";
import imageReducer from "./image/reducers";
import canvasReducer from "./canvas/reducers";
import transformationMatrixReducer from "./canvas/transformationMatrix/reducers";
import annotationReducer from "./canvas/annotation/reducers";
import utilReducer from "./Util/reducer";

const rootReducer = combineReducers({
  user: userReducer,
  project: projectReducer,
  dataset: datasetReducer,
  image: imageReducer,
  util: utilReducer,
  canvas: combineReducers({
    canvas: canvasReducer,
    transformationMatrix: transformationMatrixReducer,
    currentAnnotation: annotationReducer,
  }),
  
});

export default rootReducer;
