import React, { useState } from "react";
import ToolIcon from "./ToolIcon";
import { Switch, MenuIcon, EraserIcon } from "evergreen-ui";
import LabelInput from "./labelInput";
import "../../static/css/custom.css";
import Slider from "@material-ui/core/Slider";
import useMediaQuery from "@material-ui/core/useMediaQuery";

const Sidebar = () => {
  const [color, setcolor] = useState("#FF0000");
  const minWidth = useMediaQuery("(min-width:1260px)");
  const maxWidth = useMediaQuery("(max-width:1430px)");
  const breakPoint = minWidth && maxWidth;

  const style1 = () => {
    return {
      position: "fixed",
      top: "7%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "5%",
      height: "100%",
      backgroundColor: "#000840",
      overflowY: "auto",
      overflowX: "hidden",
    };
  };

  const style2 = () => {
    return {
      position: "fixed",
      top: "7%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "8%",
      height: "100%",
      backgroundColor: "#000840",
      overflowY: "auto",
      overflowX: "hidden",
    };
  };

  const [auto, setauto] = useState(false);
  const [bulk, setbulk] = useState(false);
  const [value, setvalue] = useState(true);
  const autoF = () => {
    setauto(!auto);
  };
  const bulkF = () => {
    setbulk(!bulk);
  };

  return (
    <div className="text-center mb-5" style={breakPoint ? style2() : style1()}>
      <div className="tool text-center ">
        <MenuIcon size={20} onClick={() => setvalue(!value)} color="white" />
        <br />
        {value ? (
          <div className="tools mb-5">
            <div className="row mt-4">
              <LabelInput />
              <div className="col-12 text-center mb-3 jusitfy-content-center">
                <ToolIcon
                  name="Polygon"
                  path="M44.075,20.64c-0.92719,0.17469 -1.74687,0.71219 -2.2575,1.505l-41.28,61.92c-0.79281,1.16906 -0.79281,2.70094 0,3.87l41.28,61.92c0.65844,0.95406 1.74688,1.51844 2.9025,1.505h82.56c1.15563,0.01344 2.24406,-0.55094 2.9025,-1.505l41.28,-61.92c0.79281,-1.16906 0.79281,-2.70094 0,-3.87l-41.28,-61.92c-0.65844,-0.95406 -1.74687,-1.51844 -2.9025,-1.505h-82.56c-0.1075,0 -0.215,0 -0.3225,0c-0.1075,0 -0.215,0 -0.3225,0zM46.655,27.52h78.69l39.0225,58.48l-39.0225,58.48h-78.69l-39.0225,-58.48z"
                />
              </div>
              <div className="col-12 text-center mb-3">
                <ToolIcon
                  name="Bounding box"
                  path="M26.875,21.5c-8.86035,0 -16.125,7.26465 -16.125,16.125v86c0,8.86035 7.26465,16.125 16.125,16.125h118.25c8.86035,0 16.125,-7.26465 16.125,-16.125v-86c0,-8.86035 -7.26465,-16.125 -16.125,-16.125zM26.875,32.25h118.25c3.02344,0 5.375,2.35156 5.375,5.375v86c0,3.02344 -2.35156,5.375 -5.375,5.375h-118.25c-3.02344,0 -5.375,-2.35156 -5.375,-5.375v-86c0,-3.02344 2.35156,-5.375 5.375,-5.375z"
                />
              </div>

              <div className="col-12 text-center mb-3">
                <ToolIcon
                  name="Circle"
                  path="M86,14.33333c-39.49552,0 -71.66667,32.17115 -71.66667,71.66667c0,39.49552 32.17115,71.66667 71.66667,71.66667c39.49552,0 71.66667,-32.17115 71.66667,-71.66667c0,-39.49552 -32.17115,-71.66667 -71.66667,-71.66667zM86,28.66667c31.74921,0 57.33333,25.58412 57.33333,57.33333c0,31.74921 -25.58412,57.33333 -57.33333,57.33333c-31.74921,0 -57.33333,-25.58412 -57.33333,-57.33333c0,-31.74921 25.58412,-57.33333 57.33333,-57.33333z"
                />
              </div>
              <div className="col-12 text-center mb-3">
                <ToolIcon
                  name="Line"
                  path="M21.50562,140.31565l118.81284,-118.81699l10.18352,10.18316l-118.81284,118.81699z"
                />
              </div>
              <div className="col-12 text-center mb-3">
                <ToolIcon
                  name="Dot"
                  path="M86,14.33333c-39.58041,0 -71.66667,32.08626 -71.66667,71.66667c0,39.58041 32.08626,71.66667 71.66667,71.66667c39.58041,0 71.66667,-32.08626 71.66667,-71.66667c0,-39.58041 -32.08626,-71.66667 -71.66667,-71.66667z"
                />
              </div>
              <div className="col-12 text-center mb-3">
                <ToolIcon
                  name="Brush"
                  path="M139.07813,19.3668c-3.48535,-0.0084 -6.9623,1.31855 -9.60781,3.95566l-44.35215,44.36895c-2.84707,-1.50332 -6.39961,-1.27656 -8.92754,0.94902l-34.17324,30.15879l2.28438,2.29277c-12.11895,12.56406 -18.41777,24.60742 -21.60078,33.64414c-3.32578,9.40625 -3.35098,15.75547 -3.35098,15.75547v1.94844l1.93164,0.18476c0,0 5.95449,0.5543 15.14238,-2.50273c8.85195,-2.94785 20.93731,-9.29707 34.48399,-22.42383l2.29277,2.28437l30.15879,-34.17324c2.22559,-2.51953 2.45234,-6.07207 0.94902,-8.91914l44.36895,-44.36055c5.27422,-5.28262 5.28262,-13.91621 0,-19.19883c-2.64551,-2.64551 -6.12246,-3.96406 -9.59941,-3.96406zM139.07813,23.6416c2.36836,0 4.73672,0.90703 6.55918,2.72949c3.63653,3.64492 3.63653,9.47344 -0.0084,13.10996l-47.03125,47.04805l1.43613,1.52012c1.31016,1.37734 1.36895,3.47695 0.10078,4.91309l-27.13535,30.75508l-24.7082,-24.7166l30.74668,-27.13535c1.43613,-1.25976 3.54414,-1.20937 4.92148,0.10078l1.52012,1.44453l47.03965,-47.03965c1.81406,-1.82246 4.18242,-2.73789 6.55918,-2.72949zM90.3,73.1c-1.18418,0 -2.15,0.96582 -2.15,2.15c0,1.18418 0.96582,2.15 2.15,2.15c1.18418,0 2.15,-0.96582 2.15,-2.15c0,-1.18418 -0.96582,-2.15 -2.15,-2.15zM79.55,77.4c-1.18418,0 -2.15,0.96582 -2.15,2.15c0,1.18418 0.96582,2.15 2.15,2.15c1.18418,0 2.15,-0.96582 2.15,-2.15c0,-1.18418 -0.96582,-2.15 -2.15,-2.15zM96.75,79.55c-1.18418,0 -2.15,0.96582 -2.15,2.15c0,1.18418 0.96582,2.15 2.15,2.15c1.18418,0 2.15,-0.96582 2.15,-2.15c0,-1.18418 -0.96582,-2.15 -2.15,-2.15zM73.1,83.85c-1.18418,0 -2.15,0.96582 -2.15,2.15c0,1.18418 0.96582,2.15 2.15,2.15c1.18418,0 2.15,-0.96582 2.15,-2.15c0,-1.18418 -0.96582,-2.15 -2.15,-2.15zM66.65,90.3c-1.18418,0 -2.15,0.96582 -2.15,2.15c0,1.18418 0.96582,2.15 2.15,2.15c1.18418,0 2.15,-0.96582 2.15,-2.15c0,-1.18418 -0.96582,-2.15 -2.15,-2.15zM60.2,96.75c-1.18418,0 -2.15,0.96582 -2.15,2.15c0,1.18418 0.96582,2.15 2.15,2.15c1.18418,0 2.15,-0.96582 2.15,-2.15c0,-1.18418 -0.96582,-2.15 -2.15,-2.15zM47.38399,104.17422l20.4418,20.4502c-13.06797,12.63125 -24.55703,18.67812 -32.7623,21.40762c-5.8957,1.96524 -9.21309,2.23399 -11.20352,2.25918c0.17637,-2.10801 0.65508,-5.80332 2.88906,-12.13574c2.99824,-8.47402 9.02832,-19.9127 20.63496,-31.98125z"
                />
              </div>
            </div>
            <div
              className="col-md-12  mt-2"
              style={{ display: "flex", justifyContent: "center" }}
            >
              <small
                className="text-white para-1"
                style={{ fontSize: "xx-small" }}
              >
                Brush Size
              </small>
            </div>

            <div className="col-md-12 px-3">
              <Slider />
            </div>
            <div className="col-md-12 text-white text-center">
              <EraserIcon size={10} />{" "}
              <small
                className="text-white para-1"
                style={{ fontSize: "xx-small" }}
              >
                Eraser
              </small>
            </div>
            <div className="col-md-12 px-3">
              <Slider />
            </div>
            <div
              className="col-12 mb-1 mt-2 text-center "
              style={{ display: "flex", justifyContent: "center" }}
            >
              <small
                className="text-white para-1"
                style={{ fontSize: "xx-small" }}
              >
                Pick Color
              </small>
            </div>
            <div
              className="col-12 mb-2"
              style={{ display: "flex", justifyContent: "center" }}
            >
              <input
                type="color"
                id="primary_color"
                className="color-pick-box-tg"
                value={color}
                onChange={(e)=>setcolor(e.target.value)}
                style={{
                  width: "28px",
                  height: "28px",
                  borderRadius: "50%",
                }}
              />
            </div>

            <div
              className="col-12 mt-4 text-center"
              style={{ display: "flex", justifyContent: "center" }}
            >
              <small
                className="text-white para-1 mb-2"
                style={{ fontSize: "xx-small" }}
              >
                Auto Increment
              </small>
            </div>
            <div
              className="col-12 mb-2"
              style={{ display: "flex", justifyContent: "center" }}
            >
              <Switch height={20} checked={auto} onChange={autoF} />
            </div>
            <div
              className="col-12 mb-2 text-center"
              style={{ display: "flex", justifyContent: "center" }}
            >
              <small
                className="text-white para-1"
                style={{ fontSize: "xx-small" }}
              >
                Bulk Increment
              </small>
            </div>
            <div
              className="col-12 mb-1"
              style={{ display: "flex", justifyContent: "center" }}
            >
              <Switch height={20} checked={bulk} onChange={bulkF} />
            </div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default Sidebar;
