import {
  SET_CURRENT_PROJECT,
  LOAD_IMAGES,
  RECIEVED_IMAGES,
  ADD_PROJECT_DETAILS,
  ATTACH_DATASETS,
  ADD_LABELS,
  RESET_IMAGES,
} from "./constants";

const initialState = {
  project: {},
  images: [],
  imagesRecieved: false,
};

const projectReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CURRENT_PROJECT:
      return {
        ...state,
        project: action.project,
      };
    case RECIEVED_IMAGES:
      return {
        ...state,
        images: action.images,
        imagesRecieved: true,
      };
    case LOAD_IMAGES:
      return {
        ...state,
        imagesRecieved: false,
      };
    case ADD_PROJECT_DETAILS:
      return {
        ...state,
        project: {
          ...state.project,
          ...action.projectDetails,
        },
      };
    case ATTACH_DATASETS:
      return {
        ...state,
        project: {
          ...state.project,
          dataset: action.datasets,
        },
      };

    case ADD_LABELS:
      return {
        ...state,
        project: {
          ...state.project,
          labels: action.labels,
        },
      };
    case RESET_IMAGES:
      return { ...state, images: [], imagesRecieved: false };

    default:
      return state;
  }
};

export default projectReducer;
