import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { recievedProjectsAndDatasets } from "../redux/user/actions";
import { privateApi } from "../Util/API";
import { isAuthenticated } from "../Util/Auth";
import Layout from "../UI/Layouts/Layout";
import Content from "../components/All-Datasets/Content";
import { RESET_IMAGES } from "../redux/dataset/constants";

const LocuslearnAllDatasets = () => {
  const state = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const handleRequest = async () => {
    let res = await privateApi(
      `/api/projects/user_all_projects/${isAuthenticated().user.user_id}/`,
      {},
      "get"
    );
    if (res.status === 200) {
      dispatch(
        recievedProjectsAndDatasets(res.body.Projects, res.body.DataSets)
      );
    }
  };

  useEffect(() => {
    dispatch({ type: RESET_IMAGES });
    if (state.datasets.length === 0) {
      handleRequest();
    }
  }, []);

  return <Layout content={Content} filter={true} />;
};

export default LocuslearnAllDatasets;
