import React from "react";
import {
  Tooltip,
  ArrowRightIcon,
  ArrowLeftIcon,
  PauseIcon,
  StopIcon,
  MenuIcon,
} from "evergreen-ui";
import "../../static/css/custom.css";
import { useDispatch, useSelector } from "react-redux";

const Topbar = () => {
  const dispatch = useDispatch();
  const image = useSelector((state) => state.image);
  const project = useSelector(state => state.project)
  let name = image.image.file_path.split("/");
  const style1 = () => {
    return {
      position: "fixed",
      top: "0",
      left: "0%",
      right: "0%",
      bottom: "0",
      width: "100%",
      height: "7%",
      backgroundColor: "#000840",
    };
  };
  return (
    <div className="header" style={style1()}>
      <div className="row px-4 tag-topbar-alignment">
        <div className="col-md-1">
          {/* <div className="ml-2 logo-login-page"></div> */}
        </div>
        <div className="col-md-8 mt-2 d-flex justify-content-between align-items-center">
          <button
            style={{ backgroundColor: "#4216d2", border: "none" }}
            className="btn rounded waves-effect text-white p-2"
          >
            <ArrowLeftIcon />
          </button>
          <p className="mt-2" style={{ color: "#fff" }}>
            {`${project.project.name} / ${name[1]}`}
          </p>
          <button
            style={{ backgroundColor: "#4216d2", border: "none" }}
            className="btn rounded waves-effect text-white p-2 "
          >
            <ArrowRightIcon />
          </button>
        </div>
        <div className="col-md-1"></div>
        <div className="col-md-2 text-white d-flex justify-content-between align-items-center">
          <Tooltip content="Save">
            <i className="fa fa-floppy-o" aria-hidden="true"></i>
          </Tooltip>
          <Tooltip content="Unknon">
            <i className="fa fa-share-square-o" aria-hidden="true"></i>
          </Tooltip>
          <button
            type="button"
            class="btn btn-light bg-white rounded waves-effect"
            style={{ fontSize: "medium", letterSpacing: "1px" }}
          >
            <span>00</span>:<span>00</span>:<span>00</span>
            <span>
              <StopIcon marginBottom={4} marginLeft={4} />
            </span>
          </button>
          <MenuIcon
            onClick={() => dispatch({ type: "TOGGLE_TAGGING_RIGHTBAR" })}
          />
        </div>
      </div>
    </div>
  );
};

export default Topbar;
