import React, { useState } from "react";
import { setCurrentDataset } from "../../redux/dataset/actions";
import { useDispatch, useSelector } from "react-redux";

export default function AttachDatasetCard(props) {
  const dispatch = useDispatch();
  const util = useSelector((state) => state.util);
  const [isSelected, setisSelected] = useState(
    util.datasets.find((x) => x.datasource_id === props.dataset.datasource_id)
  );

  const checker = (dataset) => {
    if (isSelected) {
      dispatch({
        type: "REMOVE_DATASET_ID",
        item: dataset.id,
      });
      setisSelected(false);
    } else {
      dispatch({
        type: "ADD_DATASET_ID",
        id: props.dataset,
      });
      setisSelected(true);
    }
  };

  return (
    <div className="col-lg-3 col-xl-3 col-md-3  mb-3">
      <div
        className="card shadow-sm bg"
        onClick={() => dispatch(setCurrentDataset(props.dataset))}
      >
        <div className="card-body card-height">
          <span
            onClick={() => checker(props.dataset)}
            className="badge badge-secondary badge-class"
            style={isSelected ? { backgroundColor: "#000840" } : {}}
          >
            {isSelected ? "Attached" : "Attach"}
          </span>
        </div>
      </div>
      <div className="row extra-row-class mb-2">
        <div className="project-data">
          <h5 className="project-name mt-1">
            {props.dataset !== undefined
              ? props.dataset.dataset_name.length <= 12
                ? props.dataset.dataset_name
                : `${props.dataset.dataset_name.substring(0, 12)}...`
              : "Dataset Name"}
          </h5>
          <h5 className="mt-1" style={{ fontSize: "10px", fontWeight: "400" }}>
            Images
            <span className="subhead-project ml-1">
              {" "}
              {props.dataset.images_number === undefined
                ? "(49)"
                : `(${props.dataset.images_number})`}
            </span>
          </h5>
        </div>
        <div className="project-timeline mt-7">
          <h5
            className="project-time"
            style={{ fontSize: "10px", fontWeight: "400" }}
          >
            08:52 A.M
          </h5>
          <h5
            className="project-date"
            style={{ marginTop: "0px", fontSize: "10px", fontWeight: "400" }}
          >
            Tue 07/01
          </h5>
        </div>
      </div>
    </div>
  );
}
