import React, { Component, Fragment } from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import LocuslearnLogin from "./pages/Login";
import LocuslearnSignup from "./pages/Signup";
import LocuslearnDashboard from "./pages/Dashboard";
import LocuslearnALLProjects from "./pages/AllProjects";
import LocuslearnProjectImages from "./pages/ProjectImages";
import LocuslearnAllDatasets from "./pages/AllDatasets";
import LocuslearnDatasetImages from "./pages/DatasetImages";
import LocuslearnAdminCollaborators from "./pages/AdminCollab";
import LocuslearnAdminProfile from "./pages/AdminProfile";
import LocuslearnProjectDetails from "./pages/ProjectDetails";
import LocuslearnCreateProject from "./pages/CreateNewProject";
import TaggingPage from "./pages/Tagging";
import { Router as Router, Route, Switch,} from "react-router-dom";
import { Provider } from "react-redux";
import store from "./redux/store.js";
import PublicRoute from "./config/publicRoute.config";
import PrivateRoute from "./config/privateRoute.config";

export default class Workspace extends Component {
  constructor(props) {
    super(props);
    this.state = {
      history: createBrowserHistory(),
    };
  }

  render() {
    return (
      <Provider store={store}>
        <Router history={this.state.history}>
          <Fragment>
            <div>
              <Switch>
                <PublicRoute exact path="/login" component={LocuslearnLogin} />
                <PublicRoute
                  exact
                  path="/signup"
                  component={LocuslearnSignup}
                />
                <PrivateRoute
                  exact
                  path="/dashboard"
                  component={LocuslearnDashboard}
                />
                <PrivateRoute
                  exact
                  path="/all-projects"
                  component={LocuslearnALLProjects}
                />
                <PrivateRoute
                  exact
                  path="/project-images"
                  component={LocuslearnProjectImages}
                />
                <PrivateRoute
                  exact
                  path="/all-datasets"
                  component={LocuslearnAllDatasets}
                />
                <PrivateRoute
                  exact
                  path="/dataset-images"
                  component={LocuslearnDatasetImages}
                />
                <PrivateRoute
                  exact
                  path="/admin-collaborators"
                  component={LocuslearnAdminCollaborators}
                />
                <PrivateRoute
                  exact
                  path="/admin-profile"
                  component={LocuslearnAdminProfile}
                />
                <PrivateRoute
                  exact
                  path="/project-details"
                  component={LocuslearnProjectDetails}
                />
                <PrivateRoute
                  exact
                  path="/create-newproject"
                  component={LocuslearnCreateProject}
                />
                <PrivateRoute exact path="/tagging" component={TaggingPage} />
              </Switch>
            </div>
          </Fragment>
        </Router>
      </Provider>
    );
  }
}

ReactDOM.render(<Workspace />, document.getElementById("workspace"));
