import {
  LOAD_USER,
  LOAD_PROJECTS,
  LOAD_DATASETS,
  LOAD_PROJECTS_DATASETS,
  LOAD_COLLABORATORS,
  RECIEVED_USER,
  RECIEVED_PROJECTS,
  RECIEVED_DATASETS,
  RECIEVED_PROJECTS_DATASETS,
  RECIEVED_COLLABORATORS,
  ADD_DATASET,
  ADD_PROJECT,
  ADD_COLLABORATOR,
  RESET_USER,
} from "./constants";

const initialState = {
  user: {},
  userRecieved: false,
  projects: [],
  projectsRecieved: false,
  datasets: [],
  datasetsRecieved: false,
  collaborators: [],
  collaboratorsRecieved: false,
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case RECIEVED_USER:
      return {
        ...state,
        userRecieved: true,
        user: action.user,
      };

    case RECIEVED_PROJECTS_DATASETS:
      return {
        ...state,
        datasetsRecieved: true,
        projectsRecieved: true,
        datasets: action.datasets,
        projects: action.projects,
      };

    case RECIEVED_PROJECTS:
      return {
        ...state,
        projectsRecieved: true,
        projects: action.projects,
      };

    case RECIEVED_DATASETS:
      return {
        ...state,
        datasetsRecieved: true,
        datasets: action.datasets,
      };

    case RECIEVED_COLLABORATORS:
      return {
        ...state,
        collaboratorsRecieved: true,
        collaborators: action.collaborators,
      };

    case LOAD_USER:
      return {
        ...state,
        userRecieved: false,
      };

    case LOAD_PROJECTS_DATASETS:
      return {
        ...state,
        datasetsRecieved: false,
        projectsRecieved: false,
      };

    case LOAD_PROJECTS:
      return {
        ...state,
        projectsRecieved: false,
      };

    case LOAD_DATASETS:
      return {
        ...state,
        datasetsRecieved: false,
      };

    case LOAD_COLLABORATORS:
      return {
        ...state,
        collaboratorsRecieved: false,
      };

    case ADD_PROJECT:
      return {
        ...state,
        projects: {
          ...state.projects,
          ...action.project,
        },
      };

    case ADD_DATASET:
      return {
        ...state,
        datasets: {
          ...state.datasets,
          ...action.dataset,
        },
      };

    case ADD_COLLABORATOR:
      return {
        ...state,
        collaborators: {
          ...state.collaborators,
          ...action.collaborator,
        },
      };

    case RESET_USER:
      state = initialState;
      return state;

    default:
      return state;
  }
};

export default userReducer;
