import React from "react";

export default function DatasetImageCard(props) {
  let image = `https://brightcomgroup.s3.us-east-2.amazonaws.com/${props.image}`;
  let name = props.image.split("/");
  const style1 = () => {
    return {
      backgroundImage: "url" + "(" + image + ")",
      backgroundSize: "cover",
      backgroundRepeat: "no-repeat",
      backgroundPosition: "center",
    };
  };

  return (
    <div
      className={
        props.className === undefined
          ? "col-lg-3 col-xl-3 col-md-3"
          : props.className
      }
    >
      <div
        className="card-body image-card card-height  bg mb-2"
        style={style1()}
      >
        <div className="card-body card-height"></div>
      </div>
      <div className="row extra-row-class">
        <div className="project-data">
          <p className="project-name">
            {props.image !== undefined
              ? name[1].length <= 12
                ? name[1]
                : `${name[1].substring(0, 12)}...`
              : "Image Name"}
            
          </p>
        </div>
      </div>
    </div>
  );
}
