export const isValidEmail = (email) => {
    if (email.length === 0) {
        return true;
    }
    if (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
        return true;
    }
    return false;
};


export const isAlphaNumeric = (password) => {
    var regularExpression = /^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,}$/;
    if (regularExpression.test(password)) {
        return true;
    }
    return false;
};