
  
import React from "react";
import Chart from "react-apexcharts";
import '../../static/css/custom.css'

const Donat = () => {
  const series = [20, 60,20, 17, 15,56];
  const options = {
    labels: ["Polygon", "Rectangle", "Circle", "Line", "Brush",'Dot'],
    chart: {
      width:'150px',
      type: "donut",
    },
    plotOptions: {
       
      pie: {
        startAngle: -90,
        endAngle: 270,
        donut: {
            labels: {
              show: true,
              total: {
                show: true,
                label: '',
                formatter: () => ''
              }
            }
        },
      },
    },
    dataLabels: {
      enabled: false,
      show:false,
      style: {
        colors: ['#ffffff','#ffffff']
      }
    },
    fill: {
    //   type: "gradient",
      colors: ['#cb3b3b',"#4216D2","#0b8457","#775ada",'#fc5185','#FF22226C']
    },
    legend: {
      show: false,
    },
    responsive: [
      {
        breakpoint: 480,
        options: {
          chart: {
            width: 200,
          },
          legend: {
            show:false,
            position: "bottom",
          },
        },
      },
    ],
  };

  return <Chart options={options} series={series} type="donut" width={310} />;
};

export default Donat;


