import React from "react";


export default function TitleName(props) {
  return (
    <div className="col-md-4 mt-2">
      <h5 className="text-center heads">
        {props.title}&nbsp;
      <i
          className="ml-2 fa fa-pencil-square-o color-pink"
          aria-hidden="true"
        ></i>
      </h5>
    </div>
  )
}




