import React from "react";
import { LayoutLinearIcon } from "evergreen-ui";

export default function Testers() {
  return (
    <div className="row">
      <div className="card card-realign mar-20-b-rad">
        <div className="card-body">
          <div className="row">
            <div className="col-md-6">
              <h5 className="heads">Testers</h5>
            </div>
            <div className="col-md-6">
              <i
                className="fa fa-plus-circle plus-circle-bg"
                aria-hidden="true"
              ></i>
            </div>
          </div>
          <div className="row">
            <div className="col-md-2">
              <i
                className="fa fa-circle circle-bg circle-big"
                aria-hidden="true"
              ></i>
            </div>
            <div className="col-md-7">
              <h5 className="taggers-name">Tester Name</h5>
              <p className="sub2">34%</p>
            </div>
            <div className="col-md-3 mt-1">
              <LayoutLinearIcon color="grey" />
            </div>
          </div>
          <div className="row">
            <div className="col-md-2">
              <i
                className="fa fa-circle circle-bg circle-big"
                aria-hidden="true"
              ></i>
            </div>
            <div className="col-md-7">
              <h5 className="taggers-name">Tester Name</h5>
              <p className="sub2">34%</p>
            </div>
            <div className="col-md-3 mt-1">
              <LayoutLinearIcon color="grey" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
