import React from "react";
import { Link } from "react-router-dom";
import { setCurrentDataset } from "../../redux/dataset/actions";
import { useDispatch, useSelector } from "react-redux";

export default function DatasetCard(props) {
  const dispatch = useDispatch();

  return (
    <div className="col-lg-3 col-xl-3 col-md-3">
      <Link to="/dataset-images">
        <div
          className="card-body image-card card-height bg mb-2"
          onClick={() => dispatch(setCurrentDataset(props.dataset))}
        >
          <div className="card-body card-height mt-3"></div>
        </div>
      </Link>
      <div className="row extra-row-class" >
        <div className="project-data text-left">
          <p className="project-name">
            {props.dataset !== undefined
              ? props.dataset.dataset_name.length <= 12
                ? props.dataset.dataset_name
                : `${props.dataset.dataset_name.substring(0, 12)}...`
              : "Dataset Name"}
              <p className="" style={{ fontSize: "10px", fontWeight: "400",marginTop:'-4px' }}>
            Images 
            <span className="subhead-project ml-2">
              {props.dataset.images_number === undefined ? "(49)" : `(${props.dataset.images_number})`}
            </span>
          </p>
          </p>
       
        </div>
        <div className="project-timeline ">
          <h5
            className="project-time"
            style={{ fontSize: "10px", fontWeight: "400" }}
          >
            08:52 A.M
          </h5>
          <h5
            className="project-date"
            style={{ marginTop: "0px", fontSize: "10px", fontWeight: "400" }}
          >
            Tue 07/01
          </h5>
        </div>
      </div>
    </div>
  );
}
