import React from "react";
import { Tooltip } from "evergreen-ui";
import "../../static/css/custom.css";

export default function ProgressBar() {
  return (
    <div className="col-md-4 ">
      <div className="card shadow-sm ">
        <div className="card-body">
          <div className="row">
            <div className="col-md-6">
              <p
                className="heads"
                style={{ fontWeight: "400", fontSize: "16px" }}
              >
                Completion Rate
              </p>
            </div>
            <div className="col-md-6 text-right">
              <p>
                <span className="sub-heads">December 2019</span>&nbsp;
                <i className="fa fa-caret-down" aria-hidden="true"></i>
              </p>
            </div>
          </div>
          <div className="row">
            <div className="pro-align-div-p">
              <p className="prog-percetage-left">0%</p>
            </div>
            <div className="pro-align-div-p">
              <p className="prog-percetage-left">25%</p>
            </div>
            <div className="pro-align-div">
              <p className="prog-percetage">50%</p>
            </div>
            <div className="pro-align-div">
              <p className="prog-percetage">75%</p>
            </div>
            <div className="pro-align-div">
              <p className="prog-percetage">100%</p>
            </div>
        
          </div>
          <div className="row prog-container">
            <div className="progresses"  style={{marginLeft:'-8px'}}>
              <Tooltip content="67%">
                <div
                  class="progress"
                  style={{ height: "7px", cursor: "pointer" }}
                >
                  <div
                    class="progress-bar"
                    role="progressbar"
                    style={{ width: "67%", backgroundColor: "#DD32B3" }}
                    aria-valuenow="25"
                    aria-valuemin="0"
                    aria-valuemax="100"
                  ></div>
                </div>
              </Tooltip>
            </div>
            <div className="progresses" style={{marginLeft:'-8px'}}>
              <Tooltip content="47%">
                <div
                  class="progress"
                  style={{ height: "7px", cursor: "pointer" }}
                >
                  <div
                    class="progress-bar"
                    role="progressbar"
                    style={{ width: "47%", backgroundColor: "#29CB97" }}
                    aria-valuenow="25"
                    aria-valuemin="0"
                    aria-valuemax="100"
                  ></div>
                </div>
              </Tooltip>
            </div>
            <div className="progresses" style={{marginLeft:'-8px'}}>
              <Tooltip content="67%">
                <div
                  class="progress"
                  style={{ height: "7px", cursor: "pointer" }}
                >
                  <div
                    class="progress-bar"
                    role="progressbar"
                    style={{ width: "67%", backgroundColor: "#FEC400" }}
                    aria-valuenow="67"
                    aria-valuemin="0"
                    aria-valuemax="100"
                  ></div>
                </div>
              </Tooltip>
            </div>
            <div className="progresses" style={{marginLeft:'-8px'}}>
              <Tooltip content="88%">
                <div
                  class="progress"
                  style={{ height: "7px", cursor: "pointer" }}
                >
                  <div
                    class="progress-bar"
                    role="progressbar"
                    style={{ width: "88%", backgroundColor: "#4072EE" }}
                    aria-valuenow="88"
                    aria-valuemin="0"
                    aria-valuemax="100"
                  ></div>
                </div>
              </Tooltip>
            </div>
          </div>
          <div className="row">
            <div className="col-md-6">
              <p className="sub-heads">
                <i
                  className="fa fa-circle-o progress-blue-circle"
                  aria-hidden="true"
                ></i>
                &nbsp;&nbsp;Project Name
              </p>
            </div>
            <div className="col-md-6">
              <p
                style={{ fontSize: "12px", fontWeight: 400 }}
                className="right-align sub-head-bold"
              >
                81.57%
              </p>
            </div>
            <div className="col-md-6">
              <p className="sub-heads">
                <i
                  className="fa fa-circle-o progress-violet-circle"
                  aria-hidden="true"
                ></i>
                &nbsp;&nbsp;Project Name
              </p>
            </div>
            <div className="col-md-6">
              <p className="right-align sub-head-bold">63.25%</p>
            </div>
            <div className="col-md-6">
              <p className="sub-heads">
                <i
                  className="fa fa-circle-o progress-yellow-circle"
                  aria-hidden="true"
                ></i>
                &nbsp;&nbsp;Project Name
              </p>
            </div>
            <div className="col-md-6">
              <p className="right-align sub-head-bold">63.25%</p>
            </div>
            <div className="col-md-6">
              <p className="sub-heads">
                <i
                  className="fa fa-circle-o progress-green-circle"
                  aria-hidden="true"
                ></i>
                &nbsp;&nbsp;Project Name
              </p>
            </div>
            <div className="col-md-6">
              <p className="right-align sub-head-bold">63.25%</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
