import React, { useEffect } from "react";
import { publicApi} from "../Util/API";
import { getAllDatasetImages } from "../redux/dataset/actions";
import { useSelector, useDispatch } from "react-redux";
import Layout from "../UI/Layouts/Layout";
import Content from "../components/Dataset-Images/Content";

const LocuslearnDatasetImages = () => {
  const state = useSelector((state) => state.dataset);
  const dispatch = useDispatch();

  const handleRequest = async () => {
    let data = { dataset: state.dataset };
    let res = await publicApi(`/api/images/get-images/?dataset_link=${state.dataset.dataset_link}`, data, "get");
    if (res.status === 200) {
      dispatch(getAllDatasetImages(res.body));
      return;
    }
  };

  useEffect(() => {
    handleRequest();
  }, []);

  return <Layout content={Content} filter={true} />;
};

export default LocuslearnDatasetImages;
