import React, { useState } from "react";
import "../../static/css/custom.css";
import { Link, withRouter } from "react-router-dom";
import {
  Avatar,
  ArrowLeftIcon,
  ChevronDownIcon,
  ChevronUpIcon,
} from "evergreen-ui";
import { useDispatch } from "react-redux";
import { isAuthenticated } from "../../Util/Auth";

const ExpandedSidebar = (props) => {
  const dispatch = useDispatch();
  const [filtersOpen, setfiltersOpen] = useState(false);

  const style1 = () => {
    return {
      position: "fixed",
      top: "10%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "15%",
      height: "100%",
      backgroundColor: "#000840",
    };
  };
  const style2 = () => {
    return {
      position: "fixed",
      top: "95%",
      left: "0",
      right: "0",
      bottom: "0",
      width: "15%",
      height: "5%",
      backgroundColor: "#1f4287",
      textAlign: "center",
      color: "white",
      verticalAlign: "middle",
    };
  };
  return (
    <div style={style1()}>
      <div
        onClick={() => dispatch({ type: "TOGGLE_SIDEBAR", bool: true })}
        className="text-center"
        style={style2()}
      >
        <ArrowLeftIcon size={20} marginTop={16} />
      </div>
      <div className="row mt-5">
        <div className="col-md-12 text-center">
          <Avatar
            size={85}
            backgroundColor="#dee1ec"
            name={isAuthenticated().user.username}
          />
          <br />
          <div className="text-center">
            <h5 className="text-white mb-2 mt-2">
              Welcome !
              <br />
              <span style={{ color: "#FB29FD" }}>{isAuthenticated().user.username}</span>
            </h5>
          </div>
        </div>

        {filtersOpen ? null : (
          <>
            <div className="col-md-12 text-center mt-5">
              <Link
                className={
                  window.location.pathname === "/dashboard"
                    ? "active-route"
                    : null
                }
                to="/dashboard"
              >
                Dashboard
              </Link>
            </div>
            <div className="col-md-12 mt-5 text-center">
              <Link
                className={
                  window.location.pathname === "/admin-collaborators"
                    ? "active-route"
                    : null
                }
                to="/admin-collaborators"
              >
                {" "}
                Collaborators
              </Link>
            </div>
            <div className="col-md-12 mt-5 text-center">
              <Link>
                <span className="">Notifications</span>
              </Link>
              <i
                className="fa fa-circle ml-1"
                aria-hidden="true"
                style={{ color: "#3BB54A", width: "3px", height: "3px" }}
              ></i>
            </div>
          </>
        )}
        <div
          className="col-md-12 ml-2 mt-5 text-center text-white"
          style={{ cursor: "pointer" }}
        >
          {props.filter ? (
            <>
              Apply Filters
              {filtersOpen ? (
                <ChevronUpIcon
                  marginLeft={5}
                  onClick={() => setfiltersOpen(false)}
                />
              ) : (
                <ChevronDownIcon
                  marginLeft={5}
                  onClick={() => setfiltersOpen(true)}
                />
              )}
            </>
          ) : null}
        </div>
      </div>
      {props.filter ? (
        filtersOpen ? (
          <div className="row mt-4">
            <li>
              <div className="col-md-12">
                <div className="row px-5">
                  <div className="col-md-6">
                    <label style={{ fontSize: "small", fontWeight: "400" }}>
                      Item Status
                    </label>
                  </div>
                  <div className="col-md-6">
                    <select className="fil form-select rounded ">
                      <option className="form-control rounded">Select</option>
                    </select>
                  </div>
                  <div className="col-md-6">
                    <label style={{ fontSize: "small", fontWeight: "400" }}>
                      Annotation type
                    </label>
                  </div>
                  <div className="col-md-6">
                    <select className="fil form-select rounded">
                      <option className="form-control rounded">Select</option>
                    </select>
                  </div>
                  <div className="col-md-6">
                    <label style={{ fontSize: "small", fontWeight: "400" }}>
                      Contributors
                    </label>
                  </div>
                  <div className="col-md-6">
                    <select className="fil form-select rounded">
                      <option className="form-control rounded">Select</option>
                    </select>
                  </div>
                  <div className="row px-4">
                    <div className="col-md-12 px-2">
                      <label
                        className="ml-3"
                        style={{ fontSize: "small", fontWeight: "400" }}
                      >
                        Filter by data labels
                      </label>
                    </div>
                    <div className="col-md-12 text-left">
                      <div className="search-bar-filter ml-1">
                        <input
                          style={{ height: "30px" }}
                          id="filter"
                          type="text"
                          placeholder="Search"
                          className="text-left"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-md-10 offset-md-1">
                    <div className="row ">
                      <div className="col-md-6 ">
                        <button
                          style={{
                            backgroundColor: "#4216d2",
                            border: "none",
                            fontSize: "small",
                          }}
                          className="btn rounded waves-effect text-white"
                        >
                          Apply
                        </button>
                      </div>

                      <div className="col-md-6  ">
                        <button
                          style={{
                            backgroundColor: "#ff304f",
                            border: "none",
                            fontSize: "small",
                          }}
                          className="btn rounded waves-effect text-white"
                        >
                          Reset
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </li>
          </div>
        ) : null
      ) : null}
    </div>
  );
};

export default withRouter(ExpandedSidebar);
