import React, { useState } from "react";
import "../../static/css/custom.css";
import logo from "../../static/images/check.png";
import { publicApi } from '../../Util/API';
import { isValidEmail, isAlphaNumeric } from '../../Util/Validations';
import { recievedUser } from '../../redux/user/actions';
import { useHistory, Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import Alert from '@material-ui/lab/Alert';




const SignupForm = () => {
  const [values, setvalues] = useState({
    email: "",
    password: "",
    username: "",
    first_name: "",
    confirmPassword: ""
  })
  const [error, seterror] = useState('')
  const handleInput = name => e => {
    setvalues({ ...values, [name]: e.target.value });
  }
  const history = useHistory();
  const dispatch = useDispatch();

  const handleSignup = async (e) => {
    e.preventDefault();
    values.username = values.email;
    
    if (!isValidEmail(values.email)) {
      seterror('Invalid Email Format');
      return;
    }
    if (values.password !== values.confirmPassword) {
      seterror('Confirm password is not match');
      return;
    }
    if (!isAlphaNumeric(values.password)) {
      seterror('Password should contains 8 character,atleast one special character and one number');
      return;
    }
    let res = await publicApi("/api/users/register_user/", values, "post");
    if (res.status === 400) {
      seterror(res.body.username[0]);
      return;
    }
    if (res.status === 201) {
      dispatch(recievedUser(res.body));
      return history.push('/dashboard');
    }
  }



  return (
    <div>
      <div className="mb-2">{error !== '' ? <Alert severity="error"><p className="">{error}</p></Alert> : null}</div>


      <div className="card-signup-page">
        <form onSubmit={handleSignup} >
          <div className="row">
            <div className="col-md-6">
              <div className="form-group">
                <label className="label-login-page">Full Name</label>
                <input
                  type="text"
                  className="form-control form-control-line "
                  name="user"
                  required={true}
                  onChange={handleInput('first_name')}
                  value={values.first_name}
                />
              </div>
            </div>
            <div className="col-md-6">
              <div className="row">
                <div className="col-md-6 offset-md-3 p-0">
                  <div className="avatar-login-page text-center py-4 ">
                    <h2 className="display-4 mt-2">{values.first_name !== '' ? values.first_name.charAt(0).toUpperCase() : null}</h2>
                  </div>
                </div>
                {/* <div className="col-md-8">

                  <h5 className="signup-profile-text ">
                    Update Profile picture
                  <br />
                    <span className="signup-remove-txt">Remove</span>
                  </h5>
                </div> */}
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group">
                <label className="label-login-page">Username</label>
                <input
                  type="text"
                  className="form-control form-control-line"
                  name="user"
                  required={true}
                  onChange={handleInput('username')}
                  value={values.username}
                />
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group">
                <label className="label-login-page">E-mail</label>
                <input
                  type="email"
                  className="form-control form-control-line"
                  name="user"
                  required={true}
                  onChange={handleInput('email')}
                  value={values.email}
                />
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group">
                <label className="label-login-page">Password</label>
                <input
                  type="password"
                  className="form-control form-control-line"
                  name="user"
                  required={true}
                  onChange={handleInput('password')}
                  value={values.password}
                />
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group">
                <label className="label-login-page">Confirm Password</label>
                <input
                  type="password"
                  className="form-control form-control-line"
                  name="user"
                  required={true}
                  onChange={handleInput('confirmPassword')}
                  value={values.confirmPassword}
                />
                <img src={logo} className="tick-img-align" />

              </div>
            </div>
          </div>
          <div className="row text-center">
            <input
              type="submit"
              className="btn-login-page col-md-6 offset-3 btn-sm"
              value="REGISTER"
              name="submit"
            />
          </div>
        </form>
      </div>
    </div>
  );
};

export default SignupForm;
