import axios from "axios";
import Cookies from 'js-cookie';
import { isAuthenticated } from "./Auth";



export const publicApi = (path, data, method) => {
    axios.defaults.xsrfCookieName = 'csrftoken'
    axios.defaults.xsrfHeaderName = "X-CSRFToken"
    var csrftoken = Cookies.get('csrftoken');
    return axios({
        method: method,
        url: path,
        headers: { "X-CSRFToken": csrftoken, 'Content-Type': 'application/json' },
        crossDomain: true,
        data: JSON.stringify(data),

    }).then(response => {
        return { body: response.data, status: response.status }
    }).catch(error => {
        return { body: error.response.data, status: error.response.status }
    })
}





export const privateApi = (path, data, method) => {
    axios.defaults.xsrfCookieName = 'csrftoken'
    axios.defaults.xsrfHeaderName = "X-CSRFToken"
    var csrftoken = Cookies.get('csrftoken');
    return axios({
        method: method,
        url: path,
        headers: { "X-CSRFToken": csrftoken, 'Content-Type': 'application/json', Authorization: `JWT ${isAuthenticated().token}` },
        crossDomain: true,
        data: JSON.stringify(data),

    }).then(response => {
        return { body: response.data, status: response.status }
    }).catch(error => {
        return { body: error.response.data, status: error.response.status }
    })
}