import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  PolygonFilterIcon,
  CircleIcon,
  FullCircleIcon,
  SquareIcon,
  TrashIcon,
  Button,
  PlusIcon,
} from "evergreen-ui";
import { privateApi } from "../../Util/API";
import { isAuthenticated } from "../../Util/Auth";
import { useHistory } from "react-router-dom";
import { recievedProjectsAndDatasets } from '../../redux/user/actions';

const CreateLabelsPage = () => {
  const dispatch = useDispatch();
  const options = [
    { value: "chocolate", label: "Chocolate" },
    { value: "strawberry", label: "Strawberry" },
    { value: "vanilla", label: "Vanilla" },
  ];

  const history = useHistory();

  const [showForm, setshowForm] = useState(false);

  const [values, setvalues] = useState({
    color: "#000000",
    label: "",
    tool: "Polygon",
  });

  const handleRequest2 = async () => {
    let res = await privateApi(
      `/api/projects/user_all_projects/${isAuthenticated().user.user_id}/`,
      {},
      "get"
    );
    if (res.status === 200) {
      dispatch(
        recievedProjectsAndDatasets(res.body.Projects, res.body.DataSets)
      );
    }
  };

  const handleRequest = async () => {
    let data = {
      userId: isAuthenticated().user.user_id,
      dataset: util.datasets, //[22,32,1]
      project: {
        Name: util.newProject.project_name,
        Industry: util.newProject.industry,
        Company: util.newProject.company,
        Purpose: util.newProject.purpose,
        Details: util.newProject.additional_details,
        Labels: util.labels, //[{'color'}]
      },
    };
    let res = await privateApi("/api/images/completesetup", data, "post");
    if (res.status === 200) {
      await handleRequest2();
      history.push("/all-projects");
    }
  };

  const util = useSelector((state) => state.util);

  const handleAdd = () => {
    dispatch({ type: "ADD_LABEL", label: values });

    setvalues({
      ...values,
      color: "#000000",
      label: "",
      tool: "Polygon",
    });
    setshowForm(false);
  };

  const selectIcon = (tool, color) => {
    if (tool === "Polygon") {
      return <PolygonFilterIcon color={color} />;
    } else if (tool === "Rectangle") {
      return <SquareIcon color={color} />;
    } else if (tool === "Circle") {
      return <CircleIcon color={color} />;
    }
    return <FullCircleIcon color={color} />;
  };

  const row = (label) => {
    return (
      <li class="list-group-item  d-flex justify-content-between text-center align-items-center">
        {selectIcon(label.tool, label.color)}
        <p className="mt-2">{label.label}</p>
        <div>
          <TrashIcon
            onClick={() =>
              dispatch({ type: "REMOVE_LABEL", label: label.label })
            }
            style={{ cursor: "pointer" }}
            color="#fcb1b1"
          />
        </div>
      </li>
    );
  };

  const handleChange = (name) => (e) => {
    console.log(values);
    setvalues({ ...values, [name]: e.target.value });
  };

  const form = () => {
    return (
      <>
        <div className="row d-flex align-items-center">
          <div className="col-md-2">
            <label style={{ fontSize: "x-small" }} htmlFor="color">
              Select Color
            </label>
            <input
              onChange={handleChange("color")}
              className="form-control"
              type="color"
              name="color"
              id=""
              value={values.color}
            />
          </div>
          <div className="col-md-5">
            <label style={{ fontSize: "x-small" }} htmlFor="color">
              Enter Tag Name
            </label>
            <input
              onChange={handleChange("label")}
              className="form-control"
              type="text"
              name="color"
              id=""
              value={values.label}
            />
          </div>
          <div className="col-md-3">
            <label style={{ fontSize: "x-small" }} htmlFor="tool">
              Select Tool
            </label>
            <select
              value={values.tool}
              onChange={handleChange("tool")}
              name="tool"
            >
              <option value="Polygon">Polygon</option>
              <option value="Circle">Circle</option>
              <option value="Rectangle">Rectangle</option>
              <option value="Dot">Dot</option>
            </select>
          </div>
          <div className="col-md-2 mt-4">
            <button
              onClick={handleAdd}
              disabled={values.label === ""}
              className="btn btn-block text-dark"
            >
              Add
            </button>
          </div>
        </div>
      </>
    );
  };

  return (
    <div className="mt-5">
      <div className="mt-5">
        <div className="row">
          <div className="col-md-4 offset-md-1 mt-5 text-center">
            <div className="card shadow-sm bg-white rounded">
              <div
                className="card-header d-flex justify-content-between text-center align-items-center"
                style={{ verticalAlign: "middle" }}
              >
                <div>
                  <p className="mt-2" style={{ color: "rgb(0, 8, 64)" }}>
                    Add Labels
                  </p>
                </div>

                <div
                  className="text-center text-white p-2"
                  onClick={() => setshowForm(!showForm)}
                  style={{
                    borderRadius: "50%",
                    backgroundColor: "#cabbe9",
                    width: "30px",
                    height: "30px",
                    cursor: "pointer",
                  }}
                >
                  <p
                    className="text-white"
                    style={{ fontWeight: "400", fontSize: "20px" }}
                  >
                    {showForm ? "x" : "+"}
                  </p>
                </div>
              </div>

              <ul class="list-group">
                {util.labels.map((label, index) => row(label))}
              </ul>
            </div>
            {util.labels.length !== 0 ? (
              <Button
                onClick={handleRequest}
                appearance="minimal"
                iconBefore={PlusIcon}
                className="mt-5"
              >
                Complete Setup
              </Button>
            ) : null}
          </div>
          <div className="col-md-6 mt-5">{showForm ? form() : null}</div>
        </div>
      </div>
    </div>
  );
};

export default CreateLabelsPage;
