export const getWidth = (breakpoint, noRightBar) => {
  if (breakpoint) {
    return noRightBar
      ? Math.round(window.screen.availWidth * 0.92)
      : Math.round(window.screen.availWidth * 0.72);
  } else {
    return noRightBar
      ? Math.round(window.screen.availWidth * 0.95)
      : Math.round(window.screen.availWidth * 0.77);
  }
};

export const getHeight = () => {
  return Math.round(window.screen.availHeight-(window.screen.availHeight*0.07));
};
