import React from "react";
import SignupForm from "../components/Signup/form";
import { Helmet } from "react-helmet";
import "../static/css/custom.css";
import LogoBox from "../UI/shared/Logo";

class LocuslearnSignup extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className="body">
        <Helmet>
          <style>{"body { background-color: #DEDEDE; }"}</style>
        </Helmet>
        <div className="container-fluid">
          <LogoBox />
          <div className="row mt-4">
            <div className="container">
              <div className="row">
                <div className="col-md-6 col-xl-6"></div>
                <div className="col-md-6 col-xl-6">
                  <SignupForm />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LocuslearnSignup;
